import { useContext, useMemo } from 'react';
import { I18nContext } from './i18n-context';
import { isDarkMode } from '../java-api';

import slide1_light_zh_CN from '../assets/light/scene_light_1@2x.png';
import slide2_light_zh_CN from '../assets/light/scene_light_2@2x.png';
import slide3_light_zh_CN from '../assets/light/scene_light_3@2x.png';
import slide4_light_zh_CN from '../assets/light/scene_light_4@2x.png';

import slide1_light_en_US from '../assets/light/scene_light_1_en@2x.png';
import slide2_light_en_US from '../assets/light/scene_light_2_en@2x.png';
import slide3_light_en_US from '../assets/light/scene_light_3_en@2x.png';
import slide4_light_en_US from '../assets/light/scene_light_4_en@2x.png';

import slide1_dark_zh_CN from '../assets/dark/scene_dark_1@2x.png';
import slide2_dark_zh_CN from '../assets/dark/scene_dark_2@2x.png';
import slide3_dark_zh_CN from '../assets/dark/scene_dark_3@2x.png';
import slide4_dark_zh_CN from '../assets/dark/scene_dark_4@2x.png';

import slide1_dark_en_US from '../assets/dark/scene_dark_1_en@2x.png';
import slide2_dark_en_US from '../assets/dark/scene_dark_2_en@2x.png';
import slide3_dark_en_US from '../assets/dark/scene_dark_3_en@2x.png';
import slide4_dark_en_US from '../assets/dark/scene_dark_4_en@2x.png';

import nutstoreLogo_light from '../assets/light/welcome_logo_light.svg';
import nutstoreLogo_dark from '../assets/dark/welcome_logo_dark.svg';

import bannerImage1_zh_CN from '../assets/app-recommend/banner_1.png';
import bannerImage2_zh_CN from '../assets/app-recommend/banner_2.png';
import bannerImage3_zh_CN from '../assets/app-recommend/banner_3.png';
import bannerImage4_zh_CN from '../assets/app-recommend/banner_4.png';

import bannerImage1_en_US from '../assets/app-recommend/banner_1_en.png';
import bannerImage2_en_US from '../assets/app-recommend/banner_2_en.png';
import bannerImage3_en_US from '../assets/app-recommend/banner_3_en.png';
import bannerImage4_en_US from '../assets/app-recommend/banner_4_en.png';

import aboutLogo_light from '../assets/light/about_logo_light.svg';
import aboutLogo_dark from '../assets/dark/about_logo_dark.svg';

type ImagesType = {
  slide1: string;
  slide2: string;
  slide3: string;
  slide4: string;
  nutstoreLogo: string;
};

export function useImages(): ImagesType {
  const culture = useContext(I18nContext);
  return useMemo(() => {
    if (culture === 'zh-CN') {
      if (isDarkMode()) {
        return {
          slide1: slide1_dark_zh_CN,
          slide2: slide2_dark_zh_CN,
          slide3: slide3_dark_zh_CN,
          slide4: slide4_dark_zh_CN,
          nutstoreLogo: nutstoreLogo_dark,
        };
      } else {
        return {
          slide1: slide1_light_zh_CN,
          slide2: slide2_light_zh_CN,
          slide3: slide3_light_zh_CN,
          slide4: slide4_light_zh_CN,
          nutstoreLogo: nutstoreLogo_light,
        };
      }
    } else {
      if (isDarkMode()) {
        return {
          slide1: slide1_dark_en_US,
          slide2: slide2_dark_en_US,
          slide3: slide3_dark_en_US,
          slide4: slide4_dark_en_US,
          nutstoreLogo: nutstoreLogo_dark,
        };
      } else {
        return {
          slide1: slide1_light_en_US,
          slide2: slide2_light_en_US,
          slide3: slide3_light_en_US,
          slide4: slide4_light_en_US,
          nutstoreLogo: nutstoreLogo_light,
        };
      }
    }
  }, [culture]);
}

type BannerImage = {
  banner1: string;
  banner2: string;
  banner3: string;
  banner4: string;
};

export function useBannerImages(): BannerImage {
  const culture = useContext(I18nContext);
  return useMemo(() => {
    if (culture === 'zh-CN') {
      return {
        banner1: bannerImage1_zh_CN,
        banner2: bannerImage2_zh_CN,
        banner3: bannerImage3_zh_CN,
        banner4: bannerImage4_zh_CN,
      };
    } else {
      return {
        banner1: bannerImage1_en_US,
        banner2: bannerImage2_en_US,
        banner3: bannerImage3_en_US,
        banner4: bannerImage4_en_US,
      };
    }
  }, [culture]);
}

export function useAboutLogo(): string {
  return isDarkMode() ? aboutLogo_dark : aboutLogo_light;
}
