import { useState } from 'react';
import { useReducer, useEffect } from 'react';
import produce from 'immer';
import { useDialogBuilder } from '../../components/dialog';
import { buildMessageDialog } from '../dialogs';
import { useNavigator } from '../../components/items-control';
import { SWITCH_NAME_LAUNCH, LaunchViewName } from '../../views/launch-window/window';
import { useProtectUi } from '../common';
import { useI18n } from '../../i18n';
import { WritableDraft } from 'immer/dist/internal';
import { log, sendTeaEvent } from '../../java-api';
import {
  PathSettingItem,
  SandboxInfo,
  combinePath,
  checkSyncFolderExists,
  getDefaultParentPath,
  getSandboxInfosAsync,
  applyPathSettingItemsAsync,
  closeAndEnterMainWindow,
  openDirDialogTask,
  checkForbiddenSyncFolderAsync,
  checkForbiddenParentAsync,
  openPredefineUrl,
} from '../../java-api/launch-window';

export type PathSettingModel = {
  parentDir: string;
  pathSettingItems: PathSettingItem[];
  readonly sources: SandboxInfo[];
};

type SetParentDirAction = {
  type: 'setParentDir';
  payload: string;
};

type SetItemDirAction = {
  type: 'setItemDir';
  payload: {
    id: number;
    dir: string;
  };
};

type SetItemIsSyncedToDiskAction = {
  type: 'setItemIsSyncedToDisk';
  payload: {
    id: number;
    isSyncedToDisk: boolean;
  };
};

type FillModelAction = {
  type: 'fillModelAction';
  payload: SandboxInfo[];
};

type PathSettingModelAction =
  | SetParentDirAction
  | SetItemDirAction
  | SetItemIsSyncedToDiskAction
  | FillModelAction;

function changeItemDir(item: WritableDraft<PathSettingItem>, parentDir?: string) {
  item.path = parentDir ? combinePath(parentDir, item.name) : null;
  item.isFolderAlreadyExists = !!item.path && !checkSyncFolderExists(item.path);
}

function pathSettingModelReducer(
  state: PathSettingModel,
  action: PathSettingModelAction
): PathSettingModel {
  switch (action.type) {
    case 'setParentDir':
      return produce(state, (draft) => {
        draft.parentDir = action.payload;
        draft.pathSettingItems.forEach((item) => {
          if (item.isSyncToDisk) {
            changeItemDir(item, action.payload);
          }
        });
      });
    case 'setItemDir':
      return produce(state, (draft) => {
        const item = draft.pathSettingItems.find((item) => item.id === action.payload.id);

        if (item) {
          changeItemDir(item, action.payload.dir);
        }
      });
    case 'setItemIsSyncedToDisk':
      return produce(state, (draft) => {
        const item = draft.pathSettingItems.find((item) => item.id === action.payload.id);

        if (item) {
          item.isSyncToDisk = action.payload.isSyncedToDisk;
          if (item.isSyncToDisk) {
            changeItemDir(item, draft.parentDir);
          } else {
            changeItemDir(item);
          }
        }
      });
    case 'fillModelAction':
      return produce(state, (draft) => {
        draft.parentDir = getDefaultParentPath();
        draft.sources = action.payload;
        draft.pathSettingItems = action.payload.map<PathSettingItem>((info) => {
          const path = info.isSyncToDisk ? combinePath(draft.parentDir, info.name) : null;
          return {
            id: info.id,
            isSyncToDisk: info.isSyncToDisk,
            name: info.name,
            path,
            isFolderAlreadyExists: !!path && !checkSyncFolderExists(path),
          };
        });
      });
  }
}

const initialPathSettingModel: PathSettingModel = {
  parentDir: '',
  pathSettingItems: [],
  sources: [],
};

function sendTeaEventSignUpWizard(isShareType: boolean) {
  sendTeaEvent('pc_signup_wizard', JSON.stringify({ user_choose_type: isShareType ? 2 : 1 }));
}

export default function usePathMappingModel(
  pathSettingModel?: PathSettingModel,
  inNewUserWizardProcess?: boolean
) {
  const { $t } = useI18n();
  const dialogBuilder = useDialogBuilder();
  const launchNavigator = useNavigator<LaunchViewName>(SWITCH_NAME_LAUNCH);
  const [disabledUserInput, setDisabledUserInput] = useState(false);
  const [model, dispatchModel] = useReducer(
    pathSettingModelReducer,
    pathSettingModel ?? initialPathSettingModel
  );
  useEffect(() => {
    const fetch = async () => {
      try {
        const sandboxInfos = await getSandboxInfosAsync();
        dispatchModel({
          type: 'fillModelAction',
          payload: sandboxInfos,
        });
      } catch (e) {
        log('error', `Unexpected error occurs when fulling model. error:${e}`);
      }
    };
    if (!pathSettingModel) {
      fetch();
    }
  }, [pathSettingModel]);

  // Fields

  const errorCount = model.pathSettingItems.filter((item) => item.isFolderAlreadyExists).length;

  // Functions

  const gotoDefaultPage = () => launchNavigator('path-mapping-default', { inNewUserWizardProcess });
  const gotoDefaultPageFromNewUserWizardPage = () =>
    launchNavigator('path-mapping-default', {
      pathSettingModel: model,
      inNewUserWizardProcess: true,
    });
  const gotoAdvancedPage = () =>
    launchNavigator('path-mapping-advanced', {
      pathSettingModel: model,
      inNewUserWizardProcess,
    });
  const gotoNewUserWizardPage = () =>
    launchNavigator('new-user-wizard', {
      pathSettingModel: model,
    });
  const protectUi = useProtectUi(disabledUserInput, setDisabledUserInput);
  const confirmSettings = protectUi(async (onSuccess?: () => void) => {
    for (const item of model.pathSettingItems) {
      if (item.isFolderAlreadyExists) {
        const changePath = await dialogBuilder
          .build<boolean>((close) =>
            buildMessageDialog(
              'warning',
              $t('pathMapping.messageBox.merge.title'),
              $t('pathMapping.messageBox.merge.message', item.name),
              close,
              $t('pathMapping.button.changePath'),
              $t('pathMapping.button.merge')
            )
          )
          .show();

        if (changePath) {
          gotoAdvancedPage();
          return;
        }
      }
    }

    const response = await applyPathSettingItemsAsync(model.pathSettingItems);
    switch (response.state) {
      case 'success':
        if (onSuccess) {
          onSuccess();
        }

        await closeAndEnterMainWindow(!inNewUserWizardProcess, response.defaultFolderPath);
        break;
      case 'failed':
        await dialogBuilder
          .build((close) =>
            buildMessageDialog('error', $t('messageBox.title.error'), response.message, close)
          )
          .show();
        break;
    }
  });
  const changeParentDir = protectUi(async () => {
    const dirPath = await openDirDialogTask.run();
    if (dirPath) {
      const checkResult = await checkForbiddenParentAsync(dirPath);
      switch (checkResult.result) {
        case 'valid':
          dispatchModel({
            type: 'setParentDir',
            payload: dirPath,
          });
          break;
        case 'invalid':
          await dialogBuilder
            .build((close) =>
              buildMessageDialog(
                'error',
                $t('messageBox.title.error'),
                checkResult.message ?? $t('error.unknown'),
                close
              )
            )
            .show();
          break;
      }
    }
  });
  const changeItemDir = protectUi(async (itemId: number) => {
    const dirPath = await openDirDialogTask.run();
    if (dirPath) {
      const checkResult = await checkForbiddenSyncFolderAsync(
        dirPath,
        model.pathSettingItems
          .filter((settingItem) => settingItem.path && settingItem.id !== itemId)
          .map((item) => item.path!)
      );
      switch (checkResult.result) {
        case 'valid':
          dispatchModel({
            type: 'setItemDir',
            payload: {
              id: itemId,
              dir: dirPath,
            },
          });
          break;
        case 'invalid':
          await dialogBuilder
            .build((close) =>
              buildMessageDialog(
                'error',
                $t('messageBox.title.error'),
                checkResult.message ?? $t('error.unknown'),
                close
              )
            )
            .show();
          break;
      }
    }
  });
  const setItemIsSyncedToDisk = (itemId: number, value: boolean) => {
    dispatchModel({
      type: 'setItemIsSyncedToDisk',
      payload: {
        id: itemId,
        isSyncedToDisk: value,
      },
    });
  };
  const setAllIsSyncedToDisk = (value: boolean) =>
    model.pathSettingItems.forEach((item) => setItemIsSyncedToDisk(item.id, value));
  const confirmAndOpenBackupGuideUrl = () =>
    confirmSettings(() => {
      sendTeaEventSignUpWizard(false);
      openPredefineUrl('guides-backup');
    });

  const confirmAndOpenShareGuideUrl = () =>
    confirmSettings(() => {
      sendTeaEventSignUpWizard(true);
      openPredefineUrl('guides-share');
    });

  return {
    model,
    disabledUserInput,
    errorCount,
    confirmSettings,
    changeParentDir,
    changeItemDir,
    setItemIsSyncedToDisk,
    setAllIsSyncedToDisk,
    gotoDefaultPage,
    gotoDefaultPageFromNewUserWizardPage,
    gotoAdvancedPage,
    gotoNewUserWizardPage,
    confirmAndOpenBackupGuideUrl,
    confirmAndOpenShareGuideUrl,
  };
}
