import { useI18n } from '../../i18n';
import { useState, useCallback } from 'react';
import {
  useVerifiableValue,
  useExtraError,
  usePasscodeRules,
  usePhoneNumberRules,
} from '../../hooks';
import { useNavigator, ComboBoxItem } from '../../components/items-control';
import phoneAreaCodes from '../../assets/phone-area-codes.json';
import { useProtectUi } from '../common';
import { LaunchViewName, SWITCH_NAME_LAUNCH } from '../../views/launch-window/window';
import {
  sendVerifyRegPhoneMsgAsync,
  signUpWithPhoneAsync,
  openPredefineUrl,
} from '../../java-api/launch-window';

export default function usePhoneVerificationModel() {
  const { $t } = useI18n();
  const [disabledUserInput, setDisabledUserInput] = useState(false);
  const [phoneAreaCode, setPhoneAreaCode] = useState<ComboBoxItem>(
    () => phoneAreaCodes.find((item) => item.value === '+86')!
  );
  const phoneField = useVerifiableValue<string>(
    usePhoneNumberRules(
      $t,
      useCallback(() => phoneAreaCode.value as string, [phoneAreaCode.value])
    ),
    ''
  );
  const passcodeField = useVerifiableValue<string>(usePasscodeRules($t), '');
  const [globalErrorText, setGlobalErrorText] = useExtraError([
    phoneField.value,
    passcodeField.value,
  ]);
  const [extraPhoneError, setExtraPhoneError] = useExtraError([phoneField.value]);
  const [extraPasscodeError, setExtraPasscodeError] = useExtraError([passcodeField.value]);
  const launchNavigator = useNavigator<LaunchViewName>(SWITCH_NAME_LAUNCH);

  const protectUi = useProtectUi(disabledUserInput, setDisabledUserInput);
  const sendPasscode = protectUi(async (startCountdown: () => void) => {
    setExtraPhoneError('');
    setExtraPasscodeError('');

    if (!phoneField.check()) return;

    const response = await sendVerifyRegPhoneMsgAsync(phoneField.value);
    switch (response.state) {
      case 'success':
        startCountdown();
        break;
      case 'failed':
        switch (response.errorPosition) {
          case 'global':
            setGlobalErrorText(response.message);
            break;
          case 'passcode':
            setExtraPasscodeError(response.message);
            break;
          case 'phone':
            setExtraPhoneError(response.message);
            break;
        }
        break;
    }
  });
  const confirmSignUpWithPhone = protectUi(
    async (email: string, password: string, nickname: string) => {
      if (!phoneField.check() || !passcodeField.check()) return;

      const response = await signUpWithPhoneAsync(
        email,
        password,
        nickname,
        phoneField.value,
        passcodeField.value
      );
      switch (response.state) {
        case 'success':
          launchNavigator('new-user-wizard');
          break;
        case 'failed':
          switch (response.errorPosition) {
            case 'global':
              setGlobalErrorText(response.message);
              break;
            case 'passcode':
              setExtraPasscodeError(response.message);
              break;
            case 'phone':
              setExtraPhoneError(response.message);
              break;
            default:
              setGlobalErrorText(response.message);
              break;
          }
      }
    }
  );
  const gotoSignUpPage = () => launchNavigator('sign-up');
  const openHelpUrl = () => openPredefineUrl('passcode-help');

  return {
    disabledUserInput,
    phoneAreaCode,
    phoneField: {
      ...phoneField,
      error: phoneField.error ? phoneField.error : extraPhoneError,
    },
    passcodeField: {
      ...passcodeField,
      error: passcodeField.error ? passcodeField.error : extraPasscodeError,
    },
    globalErrorText,
    setPhoneAreaCode,
    sendPasscode,
    confirmSignUpWithPhone,
    gotoSignUpPage,
    openHelpUrl,
  };
}
