import { useI18n } from '../../i18n';
import { useMemo, useState, useEffect } from 'react';
import { ComboBoxItem } from '../../components/items-control';
import { useVerifiableValue } from '../../hooks';
import { buildMessageDialog } from '../dialogs';
import { useDialogBuilder } from '../../components/dialog';
import { noRules, useIPRules, useNumberRules } from '../../hooks';
import {
  ProxyType,
  ProxySetting,
  getProxySettingsAsync,
  setProxySettingsAsync,
} from '../../java-api/launch-window';

export default function useProxySettingsModel() {
  const { $t } = useI18n();

  const proxyTypes = useMemo(
    () => [
      { value: ProxyType.noProxy, label: $t('networkSettingsDialog.proxyNoProxy') },
      { value: ProxyType.http, label: $t('networkSettingsDialog.proxyHttp') },
      { value: ProxyType.socks, label: $t('networkSettingsDialog.proxySocks') },
    ],
    [$t]
  );
  const [proxySetting, setProxySetting] = useState<ProxySetting>({ proxyType: ProxyType.noProxy });
  const [selectedProxyType, setSelectedProxyType] = useState<ComboBoxItem>(proxyTypes[0]);
  const disabledProxy = selectedProxyType.value === ProxyType.noProxy;
  const hostField = useVerifiableValue<string>(useIPRules($t), '');
  const portField = useVerifiableValue<string>(useNumberRules($t), '');
  const usernameField = useVerifiableValue<string>(noRules, '');
  const passwordField = useVerifiableValue<string>(noRules, '');

  useEffect(() => {
    const fetch = async () => {
      const savedProxySetting = await getProxySettingsAsync();
      const { proxyType, host, port, username, password } = savedProxySetting;

      setProxySetting(savedProxySetting);
      setSelectedProxyType(proxyTypes.find((item) => item.value === proxyType)!);

      hostField.setOnly(host ?? '');
      portField.setOnly(port ?? '');
      usernameField.setOnly(username ?? '');
      passwordField.setOnly(password ?? '');
    };
    fetch();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    if (selectedProxyType.value === proxySetting.proxyType) {
      const { host, port, username, password } = proxySetting;
      hostField.setOnly(host ?? '');
      portField.setOnly(port ?? '');
      usernameField.setOnly(username ?? '');
      passwordField.setOnly(password ?? '');
    } else {
      hostField.setOnly('');
      portField.setOnly('');
      usernameField.setOnly('');
      passwordField.setOnly('');
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [proxySetting, selectedProxyType]);
  const dialogBuilder = useDialogBuilder();

  const resetProxyAsync = async () => {
    const savedProxySetting = await getProxySettingsAsync();
    const { proxyType, host, port, username, password } = savedProxySetting;

    setProxySetting(savedProxySetting);
    setSelectedProxyType(proxyTypes.find((item) => item.value === proxyType)!);

    hostField.setOnly(host ?? '');
    portField.setOnly(port ?? '');
    usernameField.setOnly(username ?? '');
    passwordField.setOnly(password ?? '');
  };
  const submitProxySettings = async (close: () => void) => {
    if (!disabledProxy && (!hostField.check() || !portField.check())) return;

    const response = await setProxySettingsAsync(
      selectedProxyType.value as number,
      hostField.value.trim(),
      portField.value.trim(),
      usernameField.value.trim(),
      passwordField.value.trim()
    );
    switch (response.state) {
      case 'success':
        close();
        break;
      case 'failed':
        await dialogBuilder
          .build((close) =>
            buildMessageDialog('error', $t('messageBox.title.error'), response.message, close)
          )
          .show();
        break;
    }
  };

  return {
    proxyTypes,
    selectedProxyType,
    disabledProxy,
    hostField,
    portField,
    usernameField,
    passwordField,
    setSelectedProxyType,
    submitProxySettings,
    resetProxyAsync,
  };
}
