import { importJavaPromise } from '../../java-interop';

export enum ProxyType {
  noProxy = 0,
  http = 1,
  socks = 2,
}

// API: getProxySettings

export type ProxySetting = {
  proxyType: ProxyType;
  host?: string;
  port?: string;
  username?: string;
  password?: string;
};

export const getProxySettingsAsync = importJavaPromise<() => ProxySetting>('getProxySettings');

// API: setProxySettings

type SuccessState = {
  state: 'success';
};

type FailedState = {
  state: 'failed';
  message: string;
};

type State = SuccessState | FailedState;

export const setProxySettingsAsync = importJavaPromise<
  (proxyType: number, host?: string, port?: string, username?: string, password?: string) => State
>('setProxySettings');
