import classNames from 'classnames';
import React, { ReactNode, useCallback, useEffect, useMemo, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import { INVALID_POINT, INVALID_RECT, Point, Rect, Size } from '../common-types';

export type SplitLine = 'split-line';

export type MenuCommandItem = {
  text: string;
  onClick: (event?: React.MouseEvent) => void;
};

export type MenuItem = MenuCommandItem | SplitLine;

type ContentMenuProps = {
  items: MenuItem[];
  renderTarget: (open: (targetRect: Rect) => void) => ReactNode;
  onActiveChanged?: (value: boolean) => void;
};

const getContainerElement = (function () {
  let container: HTMLDivElement | null = null;

  return () => {
    if (container) return container;
    container = document.createElement('div');
    container.className = 'context-menu-container';
    document.body.appendChild(container);
    return container;
  };
})();

function calculatePosition(
  origin: Point,
  popupSize: Size,
  viewSize: Size,
  targetSize: Size = { width: 0, height: 0 },
  offset: number = 0
): Point {
  const x =
    origin.x + popupSize.width > viewSize.width
      ? origin.x - popupSize.width + targetSize.width
      : origin.x;
  const y =
    origin.y + targetSize.height + offset + popupSize.height > viewSize.height
      ? origin.y - popupSize.height - offset
      : origin.y + targetSize.height + offset;

  return { x, y };
}

export default function ContextMenu({ items, renderTarget, onActiveChanged }: ContentMenuProps) {
  const container = getContainerElement();
  const listRef = useRef<HTMLDivElement>(null);

  const [active, setActive] = useState(false);
  const [targetRect, setTargetRect] = useState<Rect>(INVALID_RECT);
  const [position, setPosition] = useState<Point>(INVALID_POINT);

  useEffect(() => {
    const unactive = () => setActive(false);

    container.addEventListener('click', unactive);
    container.addEventListener('auxclick', unactive);
    container.addEventListener('contextmenu', unactive);
    window.addEventListener('blur', unactive);

    return () => {
      container.removeEventListener('click', unactive);
      container.removeEventListener('auxclick', unactive);
      container.removeEventListener('contextmenu', unactive);
      window.removeEventListener('blur', unactive);
    };
  }, [container]);

  useEffect(() => {
    if (active) {
      container.classList.add('active');
    } else {
      container.classList.remove('active');
    }
  }, [active, container.classList]);

  useEffect(() => {
    if (onActiveChanged) {
      onActiveChanged(active);
    }
  }, [active, onActiveChanged]);

  useEffect(() => {
    if (active && listRef.current) {
      const newPosition = calculatePosition(
        { x: targetRect.x, y: targetRect.y },
        { width: listRef.current.offsetWidth, height: listRef.current.offsetHeight },
        { width: container.offsetWidth, height: container.offsetHeight },
        { width: targetRect.width, height: targetRect.height },
        4
      );

      setPosition(newPosition);
    }
  }, [
    active,
    container.offsetHeight,
    container.offsetWidth,
    targetRect.height,
    targetRect.width,
    targetRect.x,
    targetRect.y,
  ]);

  const updateTargetRectAndActive = useCallback((rect: Rect) => {
    setTargetRect(rect);
    setActive(true);
  }, []);

  const menuClass = classNames('context-menu-list', { active });

  const targetElement = useMemo(() => renderTarget(updateTargetRectAndActive), [
    renderTarget,
    updateTargetRectAndActive,
  ]);

  return (
    <>
      {targetElement}
      {ReactDOM.createPortal(
        <div
          ref={listRef}
          className={menuClass}
          style={{ left: `${position.x}px`, top: `${position.y}px` }}
        >
          {items.map((item, index) =>
            item === 'split-line' ? (
              <div key={`split-line-${index}`} className="split-line" />
            ) : (
              <div key={`${item.text}-${index}`} className="menu-item" onClick={item.onClick}>
                {item.text}
              </div>
            )
          )}
        </div>,
        container
      )}
    </>
  );
}
