const win = window as any;

export type Dispose = () => void;
export type EventHandler<T> = (args: T) => void;

class JavaEventCenter {
  private eventCenter: Map<string, EventHandler<any>[]> = new Map<string, EventHandler<any>[]>();

  subscribe(channel: string, handler: EventHandler<any>): Dispose {
    if (!this.eventCenter.has(channel)) {
      this.eventCenter.set(channel, []);
    }

    this.eventCenter.get(channel)!.push(handler);

    return () => {
      const handlers = this.eventCenter.get(channel);
      if (handlers) {
        const toRemovedIndex = handlers.findIndex((item) => item === handler);
        if (toRemovedIndex >= 0) {
          handlers.splice(toRemovedIndex, 1);
        }
      }
    };
  }

  publish(channel: string, json: string) {
    this.eventCenter.get(channel)?.forEach((handler) => {
      handler(json ? JSON.parse(json) : undefined);
    });
  }
}

export function subscribeJavaEvent<T>(channel: string, handler: EventHandler<T>): Dispose {
  if (!win.javaEventCenter) {
    win.javaEventCenter = new JavaEventCenter();
  }

  const eventCenter = win.javaEventCenter as JavaEventCenter;
  return eventCenter.subscribe(channel, handler);
}
