import React from 'react';
import { Space } from '../../components/layout-container';

interface NewUserWizardCardProps {
  icon: string;
  title: string;
  description: string;
  tip: string;
  onClick: () => void;
}

export default function NewUserWizardCard({
  icon,
  title,
  description,
  tip,
  onClick,
}: NewUserWizardCardProps) {
  return (
    <div className="new-user-wizard-card" onClick={() => onClick()}>
      <img src={icon} alt="icon" style={{ marginRight: '16px' }} />
      <Space direction="vertical" size="8px" align="flex-start">
        <span className="title">{title}</span>
        <span className="description">{description}</span>
      </Space>
      <div className="gap" />
      <span className="tip">{tip}</span>
    </div>
  );
}
