import { useI18n } from '../../i18n';
import { useVerifiableValue, useEmailRules, usePasswordRules, useExtraError, useNotEmptyRule } from '../../hooks';
import { useDialog, useDialogBuilder } from '../../components/dialog';
import { useState } from 'react';
import { useNavigator } from '../../components/items-control';
import {
  buildProxySettingsDialog,
  buildMessageDialog,
  buildTwoStepVerificationDialog,
} from '../dialogs';
import { LaunchViewName, SWITCH_NAME_LAUNCH } from '../../views/launch-window/window';
import { etpSSOAuthAsync, closeAndEnterMainWindow, loginAsync, openPredefineUrl, restartClient } from '../../java-api/launch-window';
import { isEnterprise, useIsFileProviderEnabled, openUrl } from '../../java-api';
import { useProtectUi } from '../common';
import { applyDefaultPathSettingsAsync } from '../../java-api/launch-window';

export default function useLoginModel() {
  const { $t } = useI18n();
  const isFileProviderEnabled = useIsFileProviderEnabled();
  const isEtp = isEnterprise();

  // Verifiable field of username for public cloud users(a.k.a email, use the email vailidation rules).
  const emailField = useVerifiableValue<string>(useEmailRules($t), '');
  // Verifiable field of username for enterprise users(email, employee id, etc, should not use email validation rules).
  const etpAccountField = useVerifiableValue<string>(useNotEmptyRule($t), '');
  const passwordField = useVerifiableValue<string>(usePasswordRules($t), '');

  const networkSettingsDialogToken = useDialog<boolean>(buildProxySettingsDialog);
  const dialogBuilder = useDialogBuilder();

  const [disabledUserInput, setDisabledUserInput] = useState(false);
  const [globalErrorText, setGlobalErrorText] = useExtraError([
    emailField.value,
    etpAccountField.value,
    passwordField.value,
  ]);
  const launchNavigator = useNavigator<LaunchViewName>(SWITCH_NAME_LAUNCH);

  const applyDefaultSettings = async () => {
  const pathSettingResponse = await applyDefaultPathSettingsAsync();
  switch(pathSettingResponse.state) {
    case 'success':
      await closeAndEnterMainWindow(false, pathSettingResponse.defaultFolderPath);
      break;
      case 'failed':
        await dialogBuilder
        .build((close) =>
          buildMessageDialog('error', $t('messageBox.title.error'), pathSettingResponse.message, close)
        )
        .show();
        break;
  }};

  const protectUi = useProtectUi(disabledUserInput, setDisabledUserInput);

  const loginInternal = async () => {
    setGlobalErrorText('');

    if(!passwordField.check()) return;

    if(isEtp && !etpAccountField.check()) return;

    if(!isEtp && !emailField.check()) return;

    const account = isEtp ? etpAccountField.value : emailField.value;
    const response = await loginAsync(account, passwordField.value);

    switch (response.state) {
      case 'success':
        if(isFileProviderEnabled) {
           await applyDefaultSettings();
        } else {
          launchNavigator('path-mapping-default');
        }
        break;
      case 'need-tf-auth':
        const title = $t(`twoStepVerificationDialog.${response.type}.title` as any);
        const message = $t(
          `twoStepVerificationDialog.${response.type}.subtitle` as any,
          response.payload ?? ''
        );
        const success = await dialogBuilder
          .build<boolean>((close) =>
            buildTwoStepVerificationDialog(
              title,
              message,
              account,
              passwordField.value,
              response.type === 'google',
              close
            )
          )
          .show();

        if (success) {
          if(isFileProviderEnabled) {
            await applyDefaultSettings();
          } else {
            launchNavigator('path-mapping-default');
          }
        }
        break;
      case 'need-setup-tf-auth':
        const confirmOpenUrl = await dialogBuilder
          .build<boolean>((close) =>
            buildMessageDialog('warning', $t('messageBox.title.info'), response.message, close)
          )
          .show();

        if (confirmOpenUrl) {
          openUrl(response.url);
        }
        break;
      case 'existing-account':
        const confirmRestart = await dialogBuilder
          .build<boolean>((close) =>
            buildMessageDialog('warning', $t('messageBox.title.info'), response.message!, close)
          )
          .show();

        if (confirmRestart) {
          restartClient();
        }
        break;
      case 'failed':
        setGlobalErrorText(response.message ? response.message : $t('error.unknown'));
    }
  };
  const confirmLogin = protectUi(loginInternal);
  const showNetworkSettingsDialog = networkSettingsDialogToken.show;
  const gotoSignUpPage = () => launchNavigator('sign-up');
  const openResetPasswordUrl = () => openPredefineUrl('reset-password');

  const etpSSOAuthInternal = async () => {
    setGlobalErrorText('');
    if (!isEtp) return;

    const response = await etpSSOAuthAsync();
    switch (response.state) {
      case 'success':
        launchNavigator('path-mapping-default');
        break;
      case 'failed':
        setGlobalErrorText(response.message ? response.message : $t('error.unknown'));
        break;
    }
  };

  return {
    disabledUserInput,
    emailField,
    etpAccountField,
    passwordField,
    globalErrorText,
    setGlobalErrorText,
    confirmLogin,
    showNetworkSettingsDialog,
    gotoSignUpPage,
    openResetPasswordUrl,
    loginInternal,
    etpSSOAuthInternal,
    protectUi,
  };
}
