import React from 'react';
import MessageDialog, { MessageDialogLevel } from '../views/launch-window/message-dialog';
import NetworkSettingsDialog from '../views/launch-window/proxy-settings-dialog';
import TwoStepVerificationDialog from '../views/launch-window/two-step-verification-dialog';

export function buildMessageDialog(
  icon: MessageDialogLevel,
  title: string,
  subtitle: string,
  close: (result: boolean) => void,
  primaryButtonText?: string,
  secondaryButtonText?: string
) {
  return (
    <MessageDialog
      icon={icon}
      title={title}
      subtitle={subtitle}
      close={close}
      primaryButtonText={primaryButtonText}
      secondaryButtonText={secondaryButtonText}
    />
  );
}

export function buildProxySettingsDialog(close: (value: boolean) => void) {
  return <NetworkSettingsDialog close={close} />;
}

export function buildTwoStepVerificationDialog(
  title: string,
  message: string,
  username: string,
  password: string,
  withGoogleAuth: boolean,
  close: (result: boolean) => void
) {
  return (
    <TwoStepVerificationDialog
      title={title}
      message={message}
      username={username}
      password={password}
      withGoogleAuth={withGoogleAuth}
      close={close}
    />
  );
}
