import React from 'react';
import { Space } from '../../components/layout-container';
import { Input } from '../../components/input';
import { Button } from '../../components/button';
import warningIcon from '../../assets/warning_icon.svg';
import { useI18n } from '../../i18n';
import usePathMappingModel, { PathSettingModel } from '../../models/launch-window/path-mapping-model';
import { LoadingButtonContent } from './loading-button-content';

export type PathMappingDefaultViewProps = {
  pathSettingModel?: PathSettingModel;
  inNewUserWizardProcess?: boolean;
};

export default function PathMappingDefaultView({
  pathSettingModel,
  inNewUserWizardProcess,
}: PathMappingDefaultViewProps) {
  const { $t } = useI18n();
  const {
    model,
    disabledUserInput,
    errorCount,
    confirmSettings,
    changeParentDir,
    gotoAdvancedPage,
    gotoNewUserWizardPage,
  } = usePathMappingModel(pathSettingModel, inNewUserWizardProcess);

  return (
    <div className="path-mapping-default-view" style={{ padding: '40px 24px 102px 24px' }}>
      <Space
        direction="vertical"
        sizes={['32px', '32px', '16px', '32px', '32px', '32px']}
        aligns={[
          'flex-start',
          'flex-start',
          'flex-start',
          'stretch',
          'flex-start',
          'flex-start',
          'flex-start',
        ]}
      >
        <span className="text title">{$t('pathMapping.default.title')}</span>
        <span className="text subtitle">{$t('pathMapping.default.subtitle')}</span>
        <span className="text subtitle">{$t('pathMapping.default.tips')}</span>
        <Input
          readOnly
          value={model.parentDir}
          addonAfter={
            <Button onClick={async () => await changeParentDir()}>
              {$t('pathMapping.default.btnBrowser')}
            </Button>
          }
        />
        <Button
          buttonType="link"
          disabled={disabledUserInput}
          onClick={() => gotoAdvancedPage()}
        >
          {$t('pathMapping.default.goToAdvanced')}
        </Button>
        {errorCount ? (
          <Space direction="horizontal" size="8px" align="flex-start">
            <img src={warningIcon} alt="warning-icon" width="16px" height="16px" />
            <span className="text tip" style={{ display: 'inline-block', lineHeight: '17px' }}>
              {$t('pathMapping.default.warningTips', errorCount)}
            </span>
          </Space>
        ) : (
          <div />
        )}
        <Button
          respondToEnter
          buttonType="primary"
          disabled={disabledUserInput}
          onClick={async () => {
            if (inNewUserWizardProcess) {
              gotoNewUserWizardPage();
            } else {
              await confirmSettings();
            }
          }}
        >
          <LoadingButtonContent isLoading={disabledUserInput}>
            {$t('button.ok')}
          </LoadingButtonContent>
        </Button>
      </Space>
    </div>
  );
}
