import { importJavaFunc, importJavaPromise } from '../../java-interop';

type ErrorPosition = 'global' | 'passcode';

type SuccessState = {
  state: 'success';
};

type NeedTfAuthState = {
  state: 'need-tf-auth';
  type: 'sms' | 'wechat' | 'google' | 'email';
  payload?: string;
};

type NeedSetupTfAuthState = {
  state: 'need-setup-tf-auth';
  message: string;
  url: string;
};

type ExistingAccountState = {
  state: 'existing-account';
  message: string;
};

type LoginFailedState = {
  state: 'failed';
  errorPosition: ErrorPosition;
  message: string;
};

// API: login

export type LoginState =
  | SuccessState
  | NeedTfAuthState
  | NeedSetupTfAuthState
  | ExistingAccountState
  | LoginFailedState;

export const loginAsync = importJavaPromise<(username: string, password: string) => LoginState>(
  'login'
);

// API: loginWithPasscode

export type LoginWithPasscodeState = SuccessState | LoginFailedState;
export const loginWithPasscodeAsync = importJavaPromise<
  (
    username: string,
    password: string,
    passcode: string,
    withGoogleAuth: boolean
  ) => LoginWithPasscodeState
>('loginWithPasscode');

// API: sendTfSms

type TfSmsFailedState = {
  state: 'failed';
  message: string;
};
export type TfSmsState = SuccessState | TfSmsFailedState;
export const sendTfSmsAsync = importJavaPromise<(email: string, password: string) => TfSmsState>(
  'sendTfSms'
);

// API: setEnterpriseServer

type SetEnterpriseServerState = SuccessState | LoginFailedState;
export const setEnterpriseServerAsync = importJavaPromise<
  (address: string, useHttps: boolean) => SetEnterpriseServerState
>('setEnterpriseServer');

// API: isEtpSSOAuthAvailable
export const isEtpSSOAuthAvailable = importJavaFunc<() => boolean>('isEtpSSOAuthAvailable', false);

// API: etpSSOAuth
export const etpSSOAuthAsync = importJavaPromise<() => LoginState>('etpSSOAuth');
