import React from 'react';
import { DataGrid } from '../../components/items-control';
import CheckBox from '../../components/input/check-box';
import { Space } from '../../components/layout-container';
import { Button } from '../../components/button';

import warningIcon from '../../assets/warning_icon.svg';
import folderIcon from '../../assets/folder.svg';
import browseFolderIcon from '../../assets/browse_folder.svg';
import { Tooltip } from '../../components/tooltip';
import { useI18n } from '../../i18n';
import usePathMappingModel, {
  PathSettingModel,
} from '../../models/launch-window/path-mapping-model';
import { LoadingButtonContent } from './loading-button-content';
import { PathSettingItem } from '../../java-api/launch-window';

export type PathMappingAdvancedViewProps = {
  pathSettingModel: PathSettingModel;
  inNewUserWizardProcess?: boolean;
};

export default function PathMappingAdvancedView({
  pathSettingModel,
  inNewUserWizardProcess,
}: PathMappingAdvancedViewProps) {
  const { $t } = useI18n();
  const {
    model,
    disabledUserInput,
    confirmSettings,
    changeItemDir,
    setItemIsSyncedToDisk,
    setAllIsSyncedToDisk,
    gotoDefaultPage,
    gotoNewUserWizardPage,
  } = usePathMappingModel(pathSettingModel, inNewUserWizardProcess);

  return (
    <div className="path-mapping-advanced-view" style={{ padding: '40px 24px 62px 24px' }}>
      <Space
        sizes={['32px', '24px', '34px']}
        aligns={['flex-start', 'flex-start', 'stretch', 'flex-start']}
        direction="vertical"
      >
        <span className="text title">{$t('pathMapping.advanced.title')}</span>
        <span className="text subtitle">{$t('pathMapping.advanced.subtitle')}</span>
        <DataGrid
          height="240px"
          itemsSource={model.pathSettingItems}
          columns={[
            {
              key: 'isSynced',
              renderHeader: () => {
                return (
                  <CheckBox
                    disabled={disabledUserInput}
                    checked={model.pathSettingItems.every((item) => item.isSyncToDisk)}
                    onClick={(e) => setAllIsSyncedToDisk((e.target as HTMLInputElement).checked)}
                  />
                );
              },
              renderCell: (value) => {
                const item = value as PathSettingItem;
                return (
                  <CheckBox
                    disabled={disabledUserInput}
                    checked={item.isSyncToDisk}
                    onClick={(e) =>
                      setItemIsSyncedToDisk(item.id, (e.target as HTMLInputElement).checked)
                    }
                  />
                );
              },
            },
            {
              key: 'name',
              renderHeader: () => (
                <span className="text subtitle">{$t('pathMapping.advanced.headerName')}</span>
              ),
              renderCell: (value) => {
                const data = value as PathSettingItem;
                return (
                  <div
                    style={{
                      display: 'flex',
                      flexDirection: 'row',
                      alignItems: 'center',
                    }}
                  >
                    <img src={folderIcon} alt="browse folder icon" width="20px" height="18px" />
                    <input
                      readOnly
                      className="text-block"
                      value={data.name}
                      style={{ width: '88px', marginLeft: '4px' }}
                    />
                  </div>
                );
              },
            },
            {
              key: 'path',
              renderHeader: () => (
                <span className="text subtitle">{$t('pathMapping.advanced.headerPath')}</span>
              ),
              renderCell: (value) => {
                const item = value as PathSettingItem;
                return (
                  <div
                    style={{
                      display: 'flex',
                      flexDirection: 'row',
                      alignItems: 'center',
                    }}
                  >
                    <Tooltip title={$t('pathMapping.advanced.warningTips')}>
                      <img
                        src={warningIcon}
                        alt="browse folder icon"
                        width="16px"
                        height="16px"
                        style={{
                          visibility: item.isFolderAlreadyExists ? 'visible' : 'hidden',
                        }}
                      />
                    </Tooltip>
                    <input
                      readOnly
                      className="text-block"
                      value={item.path ?? ''}
                      style={{
                        width: '225px',
                        marginLeft: '4px',
                        marginRight: '4px',
                      }}
                    />
                    <Button
                      title={$t('pathMapping.advanced.tooltips.changeDirButton')}
                      disabled={disabledUserInput || !item.path}
                      style={{
                        padding: 0,
                        height: '20px',
                        visibility: item.path ? 'visible' : 'hidden',
                      }}
                      onClick={async () => await changeItemDir(item.id)}
                    >
                      <img
                        src={browseFolderIcon}
                        alt="browse folder icon"
                        width="20px"
                        height="20px"
                      />
                    </Button>
                  </div>
                );
              },
            },
          ]}
        />
        <Space direction="horizontal" size="32px" align="flex-start">
          <Button
            respondToEnter
            buttonType="primary"
            disabled={disabledUserInput}
            onClick={async () => {
              if (inNewUserWizardProcess) {
                gotoNewUserWizardPage();
              } else {
                await confirmSettings();
              }
            }}
          >
            <LoadingButtonContent isLoading={disabledUserInput}>
              {$t('button.ok')}
            </LoadingButtonContent>
          </Button>
          <Button disabled={disabledUserInput} onClick={() => gotoDefaultPage()}>
            {$t('button.back')}
          </Button>
        </Space>
      </Space>
    </div>
  );
}
