import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';

import { DialogHost } from '../components/dialog';
import { SwitchHost } from '../components/items-control';
import { LanguageCode, I18nContext } from '../i18n';
import { isDarkMode, isChineseLocale, waitJavaApiReady, log } from '../java-api';

import '../styles/normalize.css';
import '../styles/index.scss';
import { getContextmenuManager } from '../hooks/use-contextmenu-manager';

function reportError(message: string) {
  try {
    log('warn', message);
  } catch {
    // Ignore
  }

  if (process.env.NODE_ENV === 'development') {
    log('error', message);
  }
}

function App({ children }: { children: React.FunctionComponentElement<any> }) {
  const [culture, setCulture] = useState<LanguageCode>('zh-CN');
  useEffect(() => {
    if (isDarkMode()) {
      document.body.classList.add('theme-dark');
    }

    setCulture(isChineseLocale() ? 'zh-CN' : 'en-US');
  }, []);

  return (
    <I18nContext.Provider value={culture}>
      <SwitchHost>
        <DialogHost>{children}</DialogHost>
      </SwitchHost>
    </I18nContext.Provider>
  );
}

/**
 * Run the specified app component after specified Java functions are registered successfully.
 *
 * Because the registration of the java function is asynchronous.
 * It is possible that the registration is not successful after the UI is displayed,
 * which will cause the call to fail.
 *
 * @param element The specified app component.
 * @param requiredJavaFuncNameList The specified Java functions list.
 */
export default function runApp(
  element: React.FunctionComponentElement<any>,
  requiredJavaFuncNameList?: ReadonlyArray<string>
) {
  // Debug
  window.onerror = (e, source, _lineno, _colno, error) => {
    const message = `error=${e}, source=${source}, name=${error?.name}, message=${error?.message} \n\n ${error?.stack}`;
    reportError(message);
  };
  window.onunhandledrejection = (error: any) => {
    const message = `PROMISE REJECT ERROR: reason=${error?.reason}, error=${error}`;
    reportError(message);
  };

  getContextmenuManager().disable();

  const render = () => {
    // Log the current browser version.
    const versionInfo = `The current browser version is [${navigator.appVersion}]`;
    try {
      log('info', versionInfo);
    } catch {
      log('error', versionInfo);
    }

    ReactDOM.render(
      <React.StrictMode>
        <App>{element}</App>
      </React.StrictMode>,
      document.getElementById('root')
    );
  };

  if (process.env.NODE_ENV === 'production') {
    waitJavaApiReady(requiredJavaFuncNameList ?? []).then(render);
  } else {
    import('../java-api/mock/index').then(render);
  }
}
