import React from 'react';
import { Space } from '../../components/layout-container';
import { Button } from '../../components/button';
import { useI18n } from '../../i18n';

import errorIcon from '../../assets/error_icon.svg';
import warningIcon from '../../assets/warning_icon.svg';
import infoIcon from '../../assets/info_icon.svg';
import successIcon from '../../assets/success_icon.svg';

export type MessageDialogLevel = 'error' | 'warning' | 'info' | 'success';

interface MessageDialogProps {
  icon: MessageDialogLevel;
  title: string;
  subtitle: string;
  primaryButtonText?: string;
  secondaryButtonText?: string;
  close: (result: boolean) => void;
}

function getIcon(level: MessageDialogLevel) {
  switch (level) {
    case 'error':
      return errorIcon;
    case 'warning':
      return warningIcon;
    case 'info':
      return infoIcon;
    case 'success':
      return successIcon;
    default:
      throw new Error(`Not found this icon (${level}).`);
  }
}

export default function MessageDialog({
  icon,
  title,
  subtitle,
  primaryButtonText,
  secondaryButtonText,
  close,
}: MessageDialogProps) {
  const { $t } = useI18n();
  return (
    <div className="message-dialog-wrapper">
      <Space
        direction="vertical"
        sizes={['16px', '32px']}
        aligns={['flex-start', 'stretch', 'flex-end']}
      >
        <Space direction="horizontal" size="16px" aligns={['stretch', 'center']}>
          <img src={getIcon(icon)} alt={icon} className="message-icon" width="40px" height="40px" />
          <span className="message-title text title">{title}</span>
        </Space>
        <span className="message-subtitle text subtitle">{subtitle}</span>
        <Space direction="horizontal" size="16px" align="center">
          <Button onClick={() => close(false)} style={{ width: '88px' }}>
            {secondaryButtonText ?? $t('button.cancel')}
          </Button>
          <Button buttonType="primary" onClick={() => close(true)} style={{ width: '88px' }}>
            {primaryButtonText ?? $t('button.ok')}
          </Button>
        </Space>
      </Space>
    </div>
  );
}
