import React, { useRef } from 'react';
import GeneralPreWindowView from './general-pre-window-view';
import { Input } from '../../components/input';
import { ComboBox } from '../../components/items-control';
import { Button } from '../../components/button';
import { Space } from '../../components/layout-container';
import phoneAreaCodes from '../../assets/phone-area-codes.json';
import { useI18n } from '../../i18n';
import { useCountdown } from '../../models/common';
import usePhoneVerificationModel from '../../models/launch-window/phone-verification-model';
import { LoadingButtonContent } from './loading-button-content';

const RETRY_INTERVAL = 60;

function CountdownButton({
  disabled,
  action,
}: {
  disabled: boolean;
  action: (startCountdown: () => void) => Promise<void>;
}) {
  const { waitingCountdown, startCountdown, remainingCount } = useCountdown(RETRY_INTERVAL);
  const { $t } = useI18n();

  return (
    <Button
      buttonType="link"
      style={{ marginRight: '16px' }}
      disabled={disabled || waitingCountdown}
      onClick={async () => {
        await action(startCountdown);
      }}
    >
      {waitingCountdown
        ? $t('twoStepVerificationDialog.waitRetry', remainingCount)
        : $t('phoneVerification.getVerificationCode')}
    </Button>
  );
}

export type PhoneVerificationViewProps = {
  email: string;
  password: string;
  nickname: string;
};

export default function PhoneVerificationView({
  email,
  password,
  nickname,
}: PhoneVerificationViewProps) {
  const { $t } = useI18n();
  const passcodeInputRef = useRef<HTMLInputElement>(null);
  const {
    disabledUserInput,
    phoneAreaCode,
    phoneField,
    passcodeField,
    globalErrorText,
    setPhoneAreaCode,
    sendPasscode,
    confirmSignUpWithPhone,
    gotoSignUpPage,
    openHelpUrl,
  } = usePhoneVerificationModel();

  return (
    <GeneralPreWindowView title={$t('phoneVerification.title')} errorText={globalErrorText}>
      <Input
        placeholder={$t('phoneVerification.phone')}
        value={phoneField.value}
        onChange={(e) => phoneField.setOnly(e.target.value)}
        errorText={phoneField.error}
        disabled={disabledUserInput}
        addonBefore={
          <ComboBox
            style={{
              borderRadius: 0,
              border: 'none',
              borderRight: '1px solid var(--border-color)',
            }}
            dropDownBoxWidth="320px"
            dorpDownBoxHeight="200px"
            selectedItem={phoneAreaCode}
            setSelectedItem={(item) => setPhoneAreaCode(item)}
            itemsSource={phoneAreaCodes}
            renderItem={({ label, value }) => <span>{`${label} (${value})`}</span>}
            renderDisplayItem={(item) => item.value as string}
          />
        }
      />
      <Input
        ref={passcodeInputRef}
        placeholder={$t('phoneVerification.verificationCode')}
        value={passcodeField.value}
        onChange={(e) => passcodeField.setOnly(e.target.value)}
        errorText={passcodeField.error}
        disabled={disabledUserInput}
        addonAfter={
          <CountdownButton
            disabled={false}
            action={async (startCountdown) => {
              await sendPasscode(startCountdown);
              passcodeInputRef.current?.focus();
            }}
          />
        }
      />
      <Button buttonType="link" onClick={() => openHelpUrl()}>
        {$t('phoneVerification.notReceivingVerificationCode')}
      </Button>
      <Space direction="horizontal" size="32px" align="center">
        <Button
          respondToEnter
          buttonType="primary"
          onClick={async () => await confirmSignUpWithPhone(email, password, nickname)}
        >
          <LoadingButtonContent isLoading={disabledUserInput}>
            {$t('phoneVerification.signup')}
          </LoadingButtonContent>
        </Button>
        <Button buttonType="link" onClick={() => gotoSignUpPage()}>
          {$t('button.previous')}
        </Button>
      </Space>
      {$t('phoneVerification.tips') ? (
        <span className="text tip">{$t('phoneVerification.tips')}</span>
      ) : null}
    </GeneralPreWindowView>
  );
}
