import React from 'react';
import GeneralPreWindowView from './general-pre-window-view';
import { Button } from '../../components/button';
import { Input, CheckBox } from '../../components/input';
import { Space } from '../../components/layout-container';
import { useI18n } from '../../i18n';
import useSignUpModel from '../../models/launch-window/sign-up-model';
import { LoadingButtonContent } from './loading-button-content';

export default function SignupView() {
  const { $t } = useI18n();
  const {
    disabledUserInput,
    showCheckServiceTermsWanring,
    emailField,
    passwordField,
    passwordAgainField,
    nicknameField,
    agreeTerms,
    globalErrorText,
    setShowCheckServiceTermsWarning,
    setAgreeTerms,
    confirmSignUp,
    gotoLoginPage,
    openUserAgreementUrl,
    openPrivacyPolicy,
    showNetworkSettingsDialog,
  } = useSignUpModel();

  return (
    <GeneralPreWindowView
      title={$t('signup.title')}
      errorText={globalErrorText}
      footer={
        <Button
          disabled={disabledUserInput}
          buttonType="link"
          onClick={async () => await showNetworkSettingsDialog()}
        >
          {$t('login.networkSettings')}
        </Button>
      }
    >
      <Input
        placeholder={$t('signup.email')}
        value={emailField.value}
        onChange={(e) => emailField.setOnly(e.target.value)}
        errorText={emailField.error}
        disabled={disabledUserInput}
      />
      <Input
        type="password"
        placeholder={$t('signup.password')}
        value={passwordField.value}
        onChange={(e) => passwordField.setOnly(e.target.value)}
        errorText={passwordField.error}
        disabled={disabledUserInput}
      />
      <Input
        type="password"
        placeholder={$t('signup.passwordAgain')}
        value={passwordAgainField.value}
        onChange={(e) => passwordAgainField.setOnly(e.target.value)}
        errorText={passwordAgainField.error}
        disabled={disabledUserInput}
      />
      <Input
        placeholder={$t('signup.nickname')}
        value={nicknameField.value}
        onChange={(e) => nicknameField.setOnly(e.target.value)}
        errorText={nicknameField.error}
        disabled={disabledUserInput}
      />
      <CheckBox
        checked={agreeTerms}
        onClick={(e) => { 
          var checked = (e.target as HTMLInputElement).checked;
          setAgreeTerms(checked);
          if(checked) setShowCheckServiceTermsWarning(false);
        }}
        disabled={disabledUserInput}
      >
        {$t('signup.policyFragment1')}
        <Button buttonType="link" onClick={() => openUserAgreementUrl()}>
          {$t('signup.policyFragment2')}
        </Button>
        {$t('signup.policyFragment3')}
        <Button buttonType="link" onClick={() => openPrivacyPolicy()}>
          {$t('signup.policyFragment4')}
        </Button>
        {showCheckServiceTermsWanring ? (
          <div className="warning">
            <div className='bubble'>
              <div className='warning_content'>
                {$t('signup.error.agreeTerms')}
              </div>
            </div>
          </div>
        ) : null}
      </CheckBox>
      <Space direction="horizontal" size="32px" align="center">
        <Button
          respondToEnter
          buttonType="primary"
          onClick={async () => await confirmSignUp()}
          disabled={disabledUserInput}
        >
          <LoadingButtonContent isLoading={disabledUserInput}>
            {$t('button.next')}
          </LoadingButtonContent>
        </Button>
        <Button onClick={() => gotoLoginPage()} disabled={disabledUserInput}>
          {$t('signup.goToSignIn')}
        </Button>
      </Space>
    </GeneralPreWindowView>
  );
}
