import React, { ReactNode } from 'react';
import { Space } from '../../components/layout-container';

interface GeneralPreWindowViewProps {
  title: string;
  children: ReactNode;
  errorText?: string;
  footer?: ReactNode;
}

export default function GeneralPreWindowView({
  title,
  children,
  errorText,
  footer,
}: GeneralPreWindowViewProps) {
  return (
    <div className="general-pre-window-view">
      <span className="text title">{title}</span>
      <span className="error-text">{errorText}</span>
      <Space direction="vertical" size="32px" align="stretch">
        {children}
      </Space>
      <div className="gap"></div>
      {footer ? <div className="footer">{footer}</div> : null}
    </div>
  );
}
