import { useMemo, useRef, DependencyList } from 'react';

/**
 * 用来声明一个点击 combo ，在 options.timeout 毫秒内，如果连续调用 click 方法
 * options.times 下，就会回调 onFire
 */
export function useClickCombo(
  options: {
    timeout: number
    times: number
    onFire: () => void
  },
  deps: DependencyList,
): {
  click(): void
  reset(): void
} {
  /**
   * 用来给 useMemo 产生的 combo 对象分配 ID ，每次当新的 combo 对象被创建时，旧
   * 的 combo 对象即使是再次被调用 click 方法，也不应该再触发 onFire 回调
   */
  const comboIdRef = useRef(0)

  return useMemo(
    () => {
      comboIdRef.current += 1
      const comboId = comboIdRef.current

      let timer: number | undefined
      let count = 0

      const reset = () => {
        count = 0
      }

      const click = () => {
        if (!timer) {
          timer = window.setTimeout(reset, options.timeout)
        }

        count += 1

        if (
          count === options.times &&
          comboId === comboIdRef.current
        ) {
          options.onFire?.()
        }
      }

      return { click, reset }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    deps,
  );
}
