import React from 'react';
import GeneralPreWindowView from './general-pre-window-view';
import { Button } from '../../components/button';
import { Input } from '../../components/input';
import { useI18n } from '../../i18n';
import useLoginEnterpriseModel from '../../models/launch-window/login-enterprise-model';
import { Space } from '../../components/layout-container';

export default function LoginEnterpriseView() {
  const { $t } = useI18n();
  const {
    disabledUserInput,
    etpAccountField,
    passwordField,
    globalErrorText,
    isSSOAuthAvailable,
    confirmLoginWithServer,
    confirmLoginWithSSO,
    showNetworkSettingsDialog,
    openLearnMoreUrl,
    gotoEnterpriseServerPage,
  } = useLoginEnterpriseModel();

  return (
    <GeneralPreWindowView
      title={$t('loginEnterprise.title')}
      errorText={globalErrorText}
      footer={
        <Button
          disabled={disabledUserInput}
          buttonType="link"
          onClick={async () => await showNetworkSettingsDialog()}
        >
          {$t('login.networkSettings')}
        </Button>
      }
    >
      <Input
        placeholder={$t('loginEnterprise.account')}
        value={etpAccountField.value}
        onChange={(e) => etpAccountField.setOnly(e.target.value)}
        errorText={etpAccountField.error}
        disabled={disabledUserInput}
      />
      <Input
        type="password"
        placeholder={$t('login.password')}
        value={passwordField.value}
        onChange={(e) => passwordField.setOnly(e.target.value)}
        errorText={passwordField.error}
        disabled={disabledUserInput}
      />
      <span className="text tip">
        {$t('loginEnterprise.description')}
        <Button buttonType="link" onClick={() => openLearnMoreUrl()} style={{ fontSize: '12px' }}>
          {$t('loginEnterprise.LearnMore')}
        </Button>
      </span>
      <Space size="8px" direction="horizontal" align="center">
        <Button
          respondToEnter
          buttonType="primary"
          disabled={disabledUserInput}
          onClick={async () => await confirmLoginWithServer()}
        >
          {$t('login.signIn')}
        </Button>
        <Button
          style={{ width: '88px' }}
          disabled={disabledUserInput}
          buttonType="link"
          onClick={() => gotoEnterpriseServerPage()}
        >
          {$t('button.previous')}
        </Button>
      </Space>
      {isSSOAuthAvailable && (
        <Button
          respondToEnter
          buttonType="link"
          disabled={disabledUserInput}
          onClick={async () => await confirmLoginWithSSO()}
        >
          {$t('loginEnterprise.SSO')}
        </Button>
      )}
    </GeneralPreWindowView>
  );
}
