import React from 'react';
import { Input } from '../../components/input';
import { ComboBox } from '../../components/items-control';
import { Space } from '../../components/layout-container';
import { Button } from '../../components/button';
import { useI18n } from '../../i18n';
import useProxySettingsModel from '../../models/launch-window/proxy-settings-model';

interface NetworkSettingsDialogProps {
  close: (result: boolean) => void;
}

export default function NetworkSettingsDialog({ close }: NetworkSettingsDialogProps) {
  const { $t } = useI18n();
  const {
    proxyTypes,
    selectedProxyType,
    disabledProxy,
    hostField,
    portField,
    usernameField,
    passwordField,
    setSelectedProxyType,
    submitProxySettings,
  } = useProxySettingsModel();

  return (
    <div className="network-settings-dialog">
      <Space
        direction="vertical"
        sizes={['24px', '8px', '32px', '8px', '32px', '8px', '32px']}
        aligns={[
          'stretch',
          'stretch',
          'stretch',
          'stretch',
          'stretch',
          'stretch',
          'stretch',
          'flex-end',
        ]}
      >
        <span className="text title">{$t('networkSettingsDialog.title')}</span>
        <span className="text subtitle">{$t('networkSettingsDialog.proxyType')}</span>
        <ComboBox
          itemsSource={proxyTypes}
          selectedItem={selectedProxyType}
          setSelectedItem={(item) => setSelectedProxyType(item)}
        />
        <span className="text subtitle">{$t('networkSettingsDialog.hostAndPort')}</span>
        <Space size="8px" direction="horizontal" align="center">
          <Input
            placeholder={$t('networkSettingsDialog.host')}
            value={hostField.value}
            onChange={(e) => hostField.setOnly(e.target.value)}
            errorText={hostField.error}
            style={{ width: '240px' }}
            disabled={disabledProxy}
          ></Input>
          <Input
            placeholder={$t('networkSettingsDialog.port')}
            value={portField.value}
            onChange={(e) => portField.setOnly(e.target.value)}
            errorText={portField.error}
            style={{ width: '124px' }}
            disabled={disabledProxy}
          ></Input>
        </Space>
        <span className="text subtitle">{$t('networkSettingsDialog.proxyAuthentication')}</span>
        <Space size="8px" direction="horizontal" align="center">
          <Input
            placeholder={$t('networkSettingsDialog.account')}
            value={usernameField.value}
            onChange={(e) => usernameField.setOnly(e.target.value)}
            errorText={usernameField.error}
            style={{ width: '240px' }}
            disabled={disabledProxy}
          ></Input>
          <Input
            placeholder={$t('networkSettingsDialog.password')}
            value={passwordField.value}
            onChange={(e) => passwordField.setOnly(e.target.value)}
            errorText={passwordField.error}
            style={{ width: '124px' }}
            disabled={disabledProxy}
          ></Input>
        </Space>
        <Space size="8px" direction="horizontal" align="center">
          <Button onClick={() => close(false)} style={{ width: '88px' }}>
            {$t('button.cancel')}
          </Button>
          <Button
            respondToEnter
            buttonType="primary"
            onClick={async () => {
              await submitProxySettings(() => close(true));
            }}
            style={{ width: '88px' }}
          >
            {$t('button.ok')}
          </Button>
        </Space>
      </Space>
    </div>
  );
}
