import React from 'react';
import { Space } from '../../components/layout-container';
import { ProgressRing } from '../../components/progress';

interface LoadingButtonContentProps {
  isLoading: boolean;
  children: string;
}

export function LoadingButtonContent({ isLoading, children }: LoadingButtonContentProps) {
  const normalText = <span>{children}</span>;
  return isLoading ? (
    <Space inline direction="horizontal" size="8px" align="center">
      <ProgressRing size="20px" />
      {normalText}
    </Space>
  ) : (
    normalText
  );
}
