import React, { ReactNode, useEffect, useRef } from 'react';
import classNames from 'classnames';

type ButtonType = 'primary' | 'default' | 'link';

interface CustomButtonProps {
  respondToEnter?: boolean;
  buttonType?: ButtonType;
  children: ReactNode;
}

type ButtonProps = CustomButtonProps & Partial<React.ButtonHTMLAttributes<HTMLButtonElement>>;

const Button: React.FC<ButtonProps> = ({
  buttonType,
  respondToEnter,
  children,
  className,
  ...props
}: ButtonProps) => {
  const { disabled } = props;
  const buttonRef = useRef<HTMLButtonElement>(null);
  useEffect(() => {
    if (respondToEnter && !disabled) {
      const handler = (e: KeyboardEvent) => {
        if (e.key === 'Enter') {
          buttonRef.current?.click();
        }
      };
      const unregisterDefaultHandler = () => {
        window.removeEventListener('keypress', handler);
      };
      const registerDefaultHandler = () => {
        unregisterDefaultHandler();
        window.addEventListener('keypress', handler);
      };

      registerDefaultHandler();
      window.addEventListener('focus', registerDefaultHandler);
      window.addEventListener('blur', unregisterDefaultHandler);

      return () => {
        window.removeEventListener('focus', registerDefaultHandler);
        window.removeEventListener('blur', unregisterDefaultHandler);
        unregisterDefaultHandler();
      };
    }
  }, [disabled, respondToEnter]);

  const buttonClass = classNames('button', {
    [`${buttonType}`]: buttonType,
    [`${className}`]: className,
  });

  return (
    <button ref={buttonRef} type="button" className={buttonClass} {...props}>
      {children}
    </button>
  );
};

Button.defaultProps = {
  buttonType: 'default',
};

export default Button;
