import React from 'react';
import { Space } from '../../components/layout-container';
import NewUserWizardCard from './new-user-wizard-card';

import backupIcon from '../../assets/new_user_wizard_backup.svg';
import shareIcon from '../../assets/new_user_wizard_share.svg';
import { Button } from '../../components/button';
import { useI18n } from '../../i18n';
import usePathMappingModel, {
  PathSettingModel,
} from '../../models/launch-window/path-mapping-model';
import { getUsername } from '../../java-api/launch-window';

export type NewUserWizardViewProps = {
  pathSettingModel?: PathSettingModel;
};

export default function NewUserWizardView({ pathSettingModel }: NewUserWizardViewProps) {
  const { $t } = useI18n();
  const {
    confirmAndOpenBackupGuideUrl,
    confirmAndOpenShareGuideUrl,
    gotoDefaultPageFromNewUserWizardPage,
  } = usePathMappingModel(pathSettingModel, true);

  return (
    <div className="new-user-wizard-view">
      <Space
        direction="vertical"
        sizes={['24px', '16px', '8px', '24px', '16px']}
        aligns={['flex-start', 'flex-start', 'flex-start', 'flex-start', 'stretch', 'stretch']}
      >
        <span className="text title">{$t('newUserWizard.title')}</span>
        <span className="text subtitle">{$t('newUserWizard.hello', getUsername())}</span>
        <span className="text subtitle">{$t('newUserWizard.subtitle')}</span>
        <span className="text tip">{$t('newUserWizard.tip')}</span>
        <NewUserWizardCard
          icon={backupIcon}
          title={$t('newUserWizard.backup')}
          description={$t('newUserWizard.backupDescription')}
          tip={$t('newUserWizard.viewDetails')}
          onClick={async () => await confirmAndOpenBackupGuideUrl()}
        />
        <NewUserWizardCard
          icon={shareIcon}
          title={$t('newUserWizard.share')}
          description={$t('newUserWizard.shareDescription')}
          tip={$t('newUserWizard.viewDetails')}
          onClick={async () => await confirmAndOpenShareGuideUrl()}
        />
      </Space>
      <div className="gap" />
      <div className="btn-settings-wrapper">
        <Button buttonType="link" onClick={() => gotoDefaultPageFromNewUserWizardPage()}>
          {$t('newUserWizard.advancedSettings')}
        </Button>
      </div>
    </div>
  );
}
