import { useCallback, useState, useRef, useEffect } from 'react';

export function useProtectUi(disabled: boolean, setDisabled: (value: boolean) => void) {
  return useCallback(
    function <T extends (...args: any) => Promise<void>>(
      action: T
    ): (...args: Parameters<T>) => Promise<void> {
      return async (...args) => {
        try {
          if (disabled) return;
          setDisabled(true);
          await action(...(args as any));
        } finally {
          setDisabled(false);
        }
      };
    },
    [disabled, setDisabled]
  );
}

export function useCountdown(count: number, intervalInMillisecond: number = 1000) {
  const [waiting, setWaiting] = useState(false);
  const [remainingCount, setRemainingCount] = useState(count);
  const remainingCountRef = useRef(count);
  useEffect(() => {
    let token: NodeJS.Timeout | undefined;
    if (waiting) {
      token = setInterval(() => {
        setRemainingCount(--remainingCountRef.current);
        if (remainingCountRef.current === 0) {
          setWaiting(false);
          setRemainingCount(count);
          remainingCountRef.current = count;
        }
      }, intervalInMillisecond);

      return () => {
        if (token) clearInterval(token);
      };
    } else if (token) {
      clearInterval(token);
    }
  }, [count, intervalInMillisecond, waiting]);

  return {
    remainingCount,
    waitingCountdown: waiting,
    startCountdown: useCallback(() => setWaiting(true), []),
    stopCountdown: useCallback(() => setWaiting(false), []),
  };
}
