const REQUIRED_JAVA_FUNCS = ['log', 'isEnterprise', 'fileProviderEnabled', 'badgeEnabled', 'isDarkMode', 'isChineseLocale'];
const win = window as any;

/**
 * Java injects these functions asynchronously, which may not be available in the early stages of web startup.
 * So we need to wait for the java function to register successfully before starting to render react dom.
 */
export default function waitJavaApiReady(funcNameList: ReadonlyArray<string>) {
  const requiredFuncList = REQUIRED_JAVA_FUNCS.concat(funcNameList);
  return new Promise<void>((resolve) => {
    function step() {
      if (requiredFuncList.every((funcName) => !!win[funcName])) {
        resolve();
      } else {
        window.requestAnimationFrame(step);
      }
    }

    window.requestAnimationFrame(step);
  });
}
