import React, { ReactNode, useEffect, useReducer } from 'react';
import classNames from 'classnames';
import { CSSTransition } from 'react-transition-group';

type SlideData = {
  selectedIndex: number;
  isStartToEnd: boolean;
};

const initSlideData: SlideData = {
  selectedIndex: 0,
  isStartToEnd: true,
};

interface CarouselProps {
  autoplay?: boolean;
  slideWidth?: number | string;
  slideHeight?: number | string;
  children: ReactNode;
}

function slideDataReducer(state: SlideData, index: number): SlideData {
  return {
    selectedIndex: index,
    isStartToEnd: index > state.selectedIndex,
  };
}

export default function Carousel({ autoplay, slideWidth, slideHeight, children }: CarouselProps) {
  const [{ selectedIndex, isStartToEnd }, dispatchSlideData] = useReducer(
    slideDataReducer,
    initSlideData
  );
  const slides = React.Children.toArray(children);
  useEffect(() => {
    if (autoplay) {
      const token = setInterval(() => {
        const index = selectedIndex + 1;
        dispatchSlideData(index >= slides.length ? 0 : index);
      }, 3000);

      return () => clearInterval(token);
    }
  }, [autoplay, selectedIndex, slides.length]);

  const slideDots = [];
  for (let i = 0; i < slides.length; i++) {
    const buttonClass = classNames('slide-dot', {
      selected: selectedIndex === i,
    });
    slideDots.push(<button key={i} className={buttonClass} onClick={() => dispatchSlideData(i)} />);
  }

  const slideItemClass = classNames('slide-item', {
    'left-to-right': !isStartToEnd,
    'right-to-left': isStartToEnd,
  });

  return (
    <div className="carousel">
      <div className="slide-list" style={{ width: slideWidth, height: slideHeight }}>
        {React.Children.map(slides, (item, index) => {
          const selected = selectedIndex === index;
          return (
            <CSSTransition in={selected} timeout={500} classNames={slideItemClass} unmountOnExit>
              <div style={{ width: slideWidth, height: slideHeight }}>{item}</div>
            </CSSTransition>
          );
        })}
      </div>
      <div className="slide-dot-list">{slideDots}</div>
    </div>
  );
}
