import useLoginModel from './login-model';
import { useVerifiableValue, useNotEmptyRule } from '../../hooks';
import { useI18n } from '../../i18n';
import { isEtpSSOAuthAvailable, setEnterpriseServerAsync } from '../../java-api/launch-window';
import { openUrl } from '../../java-api';
import { useState } from 'react';
import { useNavigator } from '../../components/items-control';
import { LaunchViewName, SWITCH_NAME_LAUNCH } from '../../views/launch-window/window';

export default function useLoginEnterpriseModel() {
  const { $t } = useI18n();
  const {
    disabledUserInput,
    etpAccountField,
    passwordField,
    globalErrorText,
    protectUi,
    loginInternal,
    etpSSOAuthInternal,
    setGlobalErrorText,
    showNetworkSettingsDialog,
  } = useLoginModel();

  const launchNavigator = useNavigator<LaunchViewName>(SWITCH_NAME_LAUNCH);

  const [useHttps, setUseHttps] = useState(true); // By default, useHttps is true.

  const isSSOAuthAvailable = isEtpSSOAuthAvailable();

  const serverAddressField = useVerifiableValue<string>(useNotEmptyRule($t), '');

  const confirmServerAddress = protectUi(async () => {
    setGlobalErrorText('');
    if (!serverAddressField.check()) return;

    const response = await setEnterpriseServerAsync(serverAddressField.value, useHttps);
    switch (response.state) {
      case 'success':
        gotoEnterpriseLoginPage();
        break;
      case 'failed':
        setGlobalErrorText(response.message);
        break;
    }
  });

  const confirmLoginWithServer = protectUi(async () => {
    await loginInternal();
  });

  const confirmLoginWithSSO = protectUi(async () => {
    await etpSSOAuthInternal();
  });

  const openLearnMoreUrl = () => openUrl('http://jianguoyun.com/s/enterprise');

  const gotoEnterpriseLoginPage = () => launchNavigator('login-enterprise');
  const gotoEnterpriseServerPage = () => launchNavigator('enterprise-server');
  
  return {
    disabledUserInput,
    serverAddressField,
    etpAccountField,
    passwordField,
    globalErrorText,
    useHttps,
    isSSOAuthAvailable,
    confirmLoginWithServer,
    confirmLoginWithSSO,
    confirmServerAddress,
    showNetworkSettingsDialog,
    openLearnMoreUrl,
    setUseHttps,
    gotoEnterpriseLoginPage,
    gotoEnterpriseServerPage,
  };
}
