import { useI18n } from '../../i18n';
import { useVerifiableValue, useExtraError, usePasscodeRules } from '../../hooks';
import { useState } from 'react';
import { useProtectUi } from '../common';
import {
  loginWithPasscodeAsync,
  sendTfSmsAsync,
  openPredefineUrl,
} from '../../java-api/launch-window';

export default function useTwoStepVerificationModel() {
  const { $t } = useI18n();
  const [disabledUserInput, setDisabledUserInput] = useState(false);
  const passcodeField = useVerifiableValue<string>(usePasscodeRules($t), '');
  const [extraPasscodeError, setExtraPasscodeError] = useExtraError([passcodeField.value]);

  const protectUi = useProtectUi(disabledUserInput, setDisabledUserInput);
  const confirmLoginWithPasscode = protectUi(
    async (
      username: string,
      password: string,
      withGoogleAuth: boolean,
      close: (value: boolean) => void
    ) => {
      setExtraPasscodeError('');

      if (!passcodeField.check()) return;

      const response = await loginWithPasscodeAsync(
        username,
        password,
        passcodeField.value,
        withGoogleAuth
      );

      switch (response.state) {
        case 'success':
          close(true);
          break;
        case 'failed':
          setExtraPasscodeError(response.message ? response.message : $t('error.unknown'));
          break;
      }
    }
  );
  const sendPasscode = protectUi(
    async (email: string, password: string, startCountdown: () => void) => {
      const response = await sendTfSmsAsync(email, password);
      switch (response.state) {
        case 'success':
          startCountdown();
          break;
        case 'failed':
          setExtraPasscodeError(response.message ? response.message : $t('error.unknown'));
          break;
      }
    }
  );
  const openHelpUrl = () => openPredefineUrl('passcode-help');

  return {
    disabledUserInput,
    passcodeField: {
      ...passcodeField,
      error: passcodeField.error ? passcodeField.error : extraPasscodeError,
    },
    confirmLoginWithPasscode,
    sendPasscode,
    openHelpUrl,
  };
}
