import React, { useEffect, useRef } from 'react';
import { Space } from '../../components/layout-container';
import { Input } from '../../components/input';
import { Button } from '../../components/button';
import { useI18n } from '../../i18n';
import { useCountdown } from '../../models/common';
import useTwoStepVerificationModel from '../../models/launch-window/two-step-verification-model';
import { LoadingButtonContent } from './loading-button-content';

const RETRY_INTERVAL = 60;

interface TwoStepVerificationDialogProps {
  title: string;
  message: string;
  username: string;
  password: string;
  withGoogleAuth: boolean;
  close: (result: boolean) => void;
}

function CountdownButton({
  disabled,
  action,
}: {
  disabled: boolean;
  action: (startCountdown: () => void) => Promise<void>;
}) {
  const { waitingCountdown, startCountdown, remainingCount } = useCountdown(RETRY_INTERVAL);
  const { $t } = useI18n();
  useEffect(() => {
    // Auto send sms.
    action(startCountdown);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Button
      buttonType="link"
      style={{ marginRight: '16px' }}
      disabled={disabled || waitingCountdown}
      onClick={async () => {
        await action(startCountdown);
      }}
    >
      {waitingCountdown
        ? $t('twoStepVerificationDialog.waitRetry', remainingCount)
        : $t('twoStepVerificationDialog.getCode')}
    </Button>
  );
}

export default function TwoStepVerificationDialog({
  title,
  message,
  username,
  password,
  withGoogleAuth,
  close,
}: TwoStepVerificationDialogProps) {
  const { $t } = useI18n();
  const inputRef = useRef<HTMLInputElement>(null);
  const {
    disabledUserInput,
    passcodeField,
    confirmLoginWithPasscode,
    sendPasscode,
    openHelpUrl,
  } = useTwoStepVerificationModel();

  return (
    <div className="two-step-verification-dialog">
      <Space
        direction="vertical"
        sizes={['24px', '24px', '32px', '32px']}
        aligns={['stretch', 'stretch', 'stretch', 'stretch', 'flex-end']}
      >
        <span className="text title">{title}</span>
        <span className="message-text">{message}</span>
        <Input
          ref={inputRef}
          value={passcodeField.value}
          onChange={(e) => passcodeField.setOnly(e.target.value)}
          placeholder={$t('twoStepVerificationDialog.verificationCode')}
          errorText={passcodeField.error}
          disabled={disabledUserInput}
          addonAfter={
            withGoogleAuth ? null : (
              <CountdownButton
                disabled={disabledUserInput}
                action={async (startCountdown) => {
                  await sendPasscode(username, password, startCountdown);
                  inputRef.current?.focus();
                }}
              />
            )
          }
        />
        <Button buttonType="link" onClick={() => openHelpUrl()}>
          {$t('twoStepVerificationDialog.notReceiveCode')}
        </Button>
        <Space direction="horizontal" size="8px" align="stretch">
          <Button
            onClick={() => close(false)}
            disabled={disabledUserInput}
            style={{ width: '88px' }}
          >
            {$t('button.cancel')}
          </Button>
          <Button
            respondToEnter
            onClick={async () =>
              await confirmLoginWithPasscode(username, password, withGoogleAuth, close)
            }
            buttonType="primary"
            disabled={disabledUserInput}
            style={{ width: '88px' }}
          >
            <LoadingButtonContent isLoading={disabledUserInput}>
              {$t('button.ok')}
            </LoadingButtonContent>
          </Button>
        </Space>
      </Space>
    </div>
  );
}
