import JavaTask from './java-task';

const win = window as any;

export type ValueWrapper<T> = {
  value: T;
};

export function importJavaFunc<T extends (...args: any) => any>(
  funcName: string,
  fallback?: ReturnType<T>
): (...args: Parameters<T>) => ReturnType<T> {
  function fallbackOrThrow(error: () => Error): ReturnType<T> {
    if (fallback !== undefined) {
      return fallback;
    } else {
      throw error();
    }
  }

  return (...args: Parameters<T>): ReturnType<T> => {
    const javaFunc = win[funcName] as Function;
    if (!javaFunc) {
      const message = `Not found this function: ${funcName}`;
      return fallbackOrThrow(() => new Error(message));
    }

    try {
      return javaFunc.call(win, ...(args as any));
    } catch (e) {
      return fallbackOrThrow(() => e);
    }
  };
}

export function importJavaPromise<T extends (...args: any) => any>(
  funcName: string
): (...args: Parameters<T>) => Promise<ReturnType<T>> {
  return async (...args: Parameters<T>) => {
    const javaTask = new JavaTask<(...args: Parameters<T>) => string>(funcName);
    try {
      const jsonString = await javaTask.run(...args);
      return JSON.parse(jsonString);
    } finally {
      javaTask.dispose();
    }
  };
}

export function importJavaPromiseNonJson<T extends (...args: any) => any>(
  funcName: string
): (...args: Parameters<T>) => Promise<ReturnType<T>> {
  return async (...args: Parameters<T>) => {
    const javaTask = new JavaTask<(...args: Parameters<T>) => ReturnType<T>>(funcName);
    try {
      return await javaTask.run(...args);
    } finally {
      javaTask.dispose();
    }
  };
}
