import React, { useEffect } from 'react';
import { Carousel, Switch, useNavigator } from '../../components/items-control';

import LoginView from './login-view';
import SignupView from './signup-view';
import PhoneVerificationView, { PhoneVerificationViewProps } from './phone-verification-view';
import NewUserWizardView, { NewUserWizardViewProps } from './new-user-wizard-view';

import { useImages } from '../../i18n';
import PathMappingDefaultView, { PathMappingDefaultViewProps } from './path-mapping-default-view';
import PathMappingAdvancedView, {
  PathMappingAdvancedViewProps,
} from './path-mapping-advanced-view';
import EnterpriseServerView from './enterprise-server-view';
import { isEnterprise } from '../../java-api';
import { useContextmenuClickCombo } from '../../hooks/use-contextmenu-manager';
import LoginEnterpriseView from './login-enterprise-view';

export const SWITCH_NAME_LAUNCH = 'switch-launch';
export type LaunchViewName =
  | 'login'
  | 'enterprise-server'
  | 'login-enterprise'
  | 'sign-up'
  | 'phone-verification'
  | 'new-user-wizard'
  | 'path-mapping-default'
  | 'path-mapping-advanced';

export default function LaunchWindow() {
  const launchNavigator = useNavigator<LaunchViewName>(SWITCH_NAME_LAUNCH);
  useEffect(() => launchNavigator(isEnterprise() ? 'enterprise-server' : 'login'), [
    launchNavigator,
  ]);
  const { slide1, slide2, slide3, slide4, nutstoreLogo } = useImages();

  const contextmenuClickCombo = useContextmenuClickCombo();

  return (
    <div className="launch-window">
      <div className="carousel-region">
        <img className="img-logo" src={nutstoreLogo} alt="logo" onClick={contextmenuClickCombo.click} />
        <Carousel autoplay slideWidth="360px" slideHeight="350px">
          {[slide1, slide2, slide3, slide4].map((item, index) => (
            <img key={item} src={item} alt={`slide-${index}`} width="360px" height="350px" />
          ))}
        </Carousel>
      </div>
      <div className="view-region">
        <Switch<LaunchViewName>
          name={SWITCH_NAME_LAUNCH}
          cases={[
            { label: 'login', renderer: () => <LoginView /> },
            { label: 'enterprise-server', renderer: () => <EnterpriseServerView /> },
            { label: 'login-enterprise', renderer: () => <LoginEnterpriseView /> },
            { label: 'sign-up', renderer: () => <SignupView /> },
            {
              label: 'phone-verification',
              renderer: ({ email, password, nickname }: PhoneVerificationViewProps) => (
                <PhoneVerificationView email={email} password={password} nickname={nickname} />
              ),
            },
            {
              label: 'new-user-wizard',
              renderer: ({ pathSettingModel }: NewUserWizardViewProps) => (
                <NewUserWizardView pathSettingModel={pathSettingModel} />
              ),
            },
            {
              label: 'path-mapping-default',
              renderer: ({
                pathSettingModel,
                inNewUserWizardProcess,
              }: PathMappingDefaultViewProps) => (
                <PathMappingDefaultView
                  pathSettingModel={pathSettingModel}
                  inNewUserWizardProcess={inNewUserWizardProcess}
                />
              ),
            },
            {
              label: 'path-mapping-advanced',
              renderer: ({
                pathSettingModel,
                inNewUserWizardProcess,
              }: PathMappingAdvancedViewProps) => (
                <PathMappingAdvancedView
                  pathSettingModel={pathSettingModel}
                  inNewUserWizardProcess={inNewUserWizardProcess}
                />
              ),
            },
          ]}
        />
      </div>
    </div>
  );
}
