export type Point = {
  x: number;
  y: number;
};

export type Size = {
  width: number;
  height: number;
};

export type Rect = Point & Size;

export const INVALID_POINT: Point = {
  x: Number.MIN_SAFE_INTEGER,
  y: Number.MIN_SAFE_INTEGER,
};

export const INVALID_SIZE: Size = {
  width: Number.MIN_SAFE_INTEGER,
  height: Number.MIN_SAFE_INTEGER,
};

export const INVALID_RECT: Rect = {
  ...INVALID_POINT,
  ...INVALID_SIZE,
};
