import React, { ReactNode, useMemo } from 'react';

type ColumnTemplate = {
  key: string;
  renderHeader: (key: string) => ReactNode;
  renderCell: (value: unknown) => ReactNode;
};

interface DataGridProps {
  itemsSource: Array<Record<string, unknown>>;
  columns: Array<ColumnTemplate>;
  height?: number | string;
}

export default function DataGrid({ itemsSource, columns, height }: DataGridProps) {
  const headers = useMemo(() => {
    return columns.map((col) => col.key);
  }, [columns]);

  return (
    <div className="data-grid-wrapper" style={{ height }}>
      <table>
        <thead>
          <tr>
            {headers.map((header, index) => (
              <th key={`${header}-${index}`}>
                {columns.find((col) => col.key === header)!.renderHeader(header)}
              </th>
            ))}
          </tr>
        </thead>
        <tbody style={{ height }}>
          {itemsSource.map((item, index) => (
            <tr key={index}>
              {headers.map((header, index) => (
                <td key={`${header}-${index}`}>
                  {columns.find((col) => col.key === header)!.renderCell(item)}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
