import { importJavaPromise } from '../../java-interop';

type ErrorPosition = 'global' | 'email' | 'phone' | 'passcode';

type OkResponse = {
  state: 'success';
};

type FailedResponse = {
  state: 'failed';
  errorPosition: ErrorPosition;
  message: string;
};

type NeedPhoneResponse = {
  state: 'need-phone';
};

type SignUpResponse = OkResponse | FailedResponse | NeedPhoneResponse;

// API: signUp

export const signUpAsync = importJavaPromise<
  (email: string, password: string, nickname: string) => SignUpResponse
>('signUp');

// API: signUpWithPhone

export const signUpWithPhoneAsync = importJavaPromise<
  (
    email: string,
    password: string,
    nickname: string,
    phone: string,
    passcode: string
  ) => SignUpResponse
>('signUpWithPhone');

// API: sendVerifyRegPhoneMsg

export const sendVerifyRegPhoneMsgAsync = importJavaPromise<(phone: string) => SignUpResponse>(
  'sendVerifyRegPhoneMsg'
);
