import React from 'react';
import GeneralPreWindowView from './general-pre-window-view';
import { Button } from '../../components/button';
import { Input } from '../../components/input';
import { Space } from '../../components/layout-container';
import { useI18n } from '../../i18n';
import useLoginModel from '../../models/launch-window/login-model';
import { LoadingButtonContent } from './loading-button-content';

export default function LoginView() {
  const { $t } = useI18n();

  const {
    disabledUserInput,
    emailField,
    passwordField,
    globalErrorText,
    confirmLogin,
    showNetworkSettingsDialog,
    gotoSignUpPage,
    openResetPasswordUrl,
  } = useLoginModel();

  return (
    <GeneralPreWindowView
      title={$t('login.title')}
      errorText={globalErrorText}
      footer={
        <Button
          disabled={disabledUserInput}
          buttonType="link"
          onClick={async () => await showNetworkSettingsDialog()}
        >
          {$t('login.networkSettings')}
        </Button>
      }
    >
      <Input
        placeholder={$t('login.email')}
        value={emailField.value}
        onChange={(e) => emailField.setOnly(e.target.value)}
        errorText={emailField.error}
        disabled={disabledUserInput}
      />
      <Input
        type="password"
        placeholder={$t('login.password')}
        value={passwordField.value}
        onChange={(e) => passwordField.setOnly(e.target.value)}
        errorText={passwordField.error}
        disabled={disabledUserInput}
      />
      <Button buttonType="link" onClick={() => openResetPasswordUrl()}>
        {$t('login.forgetPassword')}
      </Button>
      <Space direction="horizontal" size="32px" align="center">
        <Button
          respondToEnter
          buttonType="primary"
          disabled={disabledUserInput}
          onClick={async () => await confirmLogin()}
        >
          <LoadingButtonContent isLoading={disabledUserInput}>
            {$t('login.signIn')}
          </LoadingButtonContent>
        </Button>
        <Button disabled={disabledUserInput} onClick={() => gotoSignUpPage()}>
          {$t('login.goToSignUp')}
        </Button>
      </Space>
      <span className="text tip-secondary">{$t('login.enterpriseTips')}</span>
    </GeneralPreWindowView>
  );
}
