import React, { ReactNode, useEffect, CSSProperties } from 'react';

type Align = 'flex-start' | 'center' | 'flex-end' | 'baseline' | 'stretch';

const SIZE_REGEX = /^(\d+?)\*$/;

interface SpaceProps {
  direction: 'vertical' | 'horizontal';
  align?: Align;
  aligns?: Array<Align>;
  size?: number | string;
  sizes?: Array<number | string>;
  inline?: boolean;
  children: ReactNode;
}

function assertProps<T>(propName: string, count: number, value?: T, values?: Array<T>) {
  if (values) {
    if (values.length < count) {
      throw new Error(
        `The length of the ${propName}s (${values.length}) must be greater than ${count}.`
      );
    }
  } else if (!value) {
    throw new Error(`At least one of the ${propName} and the ${propName}s must be assigned.`);
  }
}

export default function Space({
  direction,
  align,
  aligns,
  size,
  sizes,
  inline,
  children,
}: SpaceProps) {
  useEffect(() => {
    if (!children) return;
    const childrenCount = React.Children.count(children);

    assertProps('size', childrenCount - 1, size, sizes);
    assertProps('align', childrenCount, align, aligns);
  }, [align, aligns, children, size, sizes]);

  return (
    <div
      className="space-wrapper"
      style={{
        display: inline ? 'inline-flex' : 'flex',
        flexDirection: direction === 'vertical' ? 'column' : 'row',
      }}
    >
      {React.Children.map(children, (item, index) => {
        const alignSelf = aligns ? aligns[index] : align;
        let style: CSSProperties = { alignSelf };
        if (index !== 0) {
          const marginSize = sizes ? sizes[index - 1] : size;
          let isStarMark = false;
          if (typeof marginSize === 'string') {
            const matchGroup = SIZE_REGEX.exec(marginSize);
            if (matchGroup) {
              style = { flex: Number.parseInt(matchGroup[1]), ...style };
              isStarMark = true;
            }
          }

          if (!isStarMark) {
            style =
              direction === 'vertical'
                ? { marginTop: marginSize, ...style }
                : { marginLeft: marginSize, ...style };
          }
        }

        return (
          <div key={`space-item-${index}`} style={style}>
            {item}
          </div>
        );
      })}
    </div>
  );
}
