import { importJavaFunc, importJavaPromise, JavaTask } from '../../java-interop';

export type SandboxInfo = {
  id: number;
  name: string;
  isSyncToDisk: boolean;
};

export type PathSettingItem = {
  path: string | null;
  isFolderAlreadyExists: boolean;
} & SandboxInfo;

export const getDefaultParentPath = importJavaFunc<() => string>('getDefaultParentPath');

export const getSandboxInfosAsync = importJavaPromise<() => SandboxInfo[]>('getSandboxInfos');

export const checkSyncFolderExists = importJavaFunc<(path: string) => boolean>(
  'checkSyncFolderExists'
);
export const combinePath = importJavaFunc<(...paths: string[]) => string>('combinePath');
export const openDirDialogTask = new JavaTask<() => string | null>('openDirDialog');

// API: applyPathSettingItems
type SuccessToApplySandboxResponse = {
  state: 'success';
  defaultFolderPath: string;
};
type FailedToApplySandboxResponse = {
  state: 'failed';
  message: string;
};
type ApplySandboxResponse = SuccessToApplySandboxResponse | FailedToApplySandboxResponse;

const applyPathSettingItems = new JavaTask<(json: string) => string>('applyPathSettingItems');
export async function applyPathSettingItemsAsync(
  items: PathSettingItem[]
): Promise<ApplySandboxResponse> {
  try {
    const jsonString = await applyPathSettingItems.run(JSON.stringify(items));
    return JSON.parse(jsonString);
  } finally {
    applyPathSettingItems.dispose();
  }
}

// API: applyDefaultPathSettings
const applyDefaultPathSettings = new JavaTask<() => string>('applyDefaultPathSettings');
export async function applyDefaultPathSettingsAsync(): Promise<ApplySandboxResponse> {
  try {
    const jsonString = await applyDefaultPathSettings.run();
    return JSON.parse(jsonString);
  } finally {
    applyDefaultPathSettings.dispose();
  }
}

// API: checkForbiddenSyncFolder
type ValidSyncFolderResult = {
  result: 'valid';
};

type ForbiddenSyncFolderResult = {
  result: 'invalid';
  errorCode: string;
  message?: string;
};

type CheckResult = ValidSyncFolderResult | ForbiddenSyncFolderResult;

export const checkForbiddenSyncFolderAsync = importJavaPromise<
  (path: string, existingPaths: string[]) => CheckResult
>('checkForbiddenSyncFolder');

export const checkForbiddenParentAsync = importJavaPromise<
  (path: string) => CheckResult
>('checkForbiddenParent');

// API: getUsername
export const getUsername = importJavaFunc<() => string>('getUsername');
