import React, { useRef, useEffect, useState, ReactElement } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';

interface TooltipProps {
  title: string;
  width?: string;
  children: ReactElement<any>;
  placement?: 'top' | 'bottom' | 'left' | 'right';
}

const getContainerElement = (function () {
  let container: HTMLDivElement | null = null;

  return () => {
    if (container) return container;
    container = document.createElement('div');
    container.className = 'tooltip-container';
    document.body.appendChild(container);
    return container;
  };
})();

type Point = {
  x: number;
  y: number;
};

const InvalidPoint = {
  x: Number.MIN_SAFE_INTEGER,
  y: Number.MIN_SAFE_INTEGER,
};

export default function Tooltip({ title, width, children, placement = 'bottom' }: TooltipProps) {
  const contentRef = useRef<HTMLDivElement>(null);
  const tooltipRef = useRef<HTMLSpanElement>(null);
  const [position, setPosition] = useState<Point>(InvalidPoint);
  const [active, setActive] = useState(false);
  useEffect(() => {
    if (!active) {
      setPosition(InvalidPoint);
      return;
    }

    if (active && contentRef.current && tooltipRef.current) {
      const contentRect = contentRef.current.getBoundingClientRect();
      const tooltipRect = tooltipRef.current.getBoundingClientRect();

      switch (placement) {
        case 'top':
          setPosition({
            x: contentRect.x + (contentRect.width - tooltipRect.width) / 2,
            y: contentRect.y - tooltipRect.height - 8,
          });
          break;
        case 'bottom':
          setPosition({
            x: contentRect.x + (contentRect.width - tooltipRect.width) / 2,
            y: contentRect.y + contentRect.height + 8,
          });
          break;
        case 'left':
          setPosition({
            x: contentRect.x - tooltipRect.width - 8,
            y: contentRect.y + (contentRect.height - tooltipRect.height) / 2,
          });
          break;
        case 'right':
          setPosition({
            x: contentRect.x + contentRect.width + 8,
            y: contentRect.y + (contentRect.height - tooltipRect.height) / 2,
          });
          break;
        default:
          setPosition(InvalidPoint);
          break;
      }
    }
  }, [active, placement]);

  const tooltipRoot = getContainerElement();
  const contentClass = classNames(children.props.className, 'tooltip-content');
  const tooltipClass = classNames('tooltip-text', { active });
  return (
    <>
      {React.cloneElement(children, {
        ref: contentRef,
        className: contentClass,
        onMouseEnter: () => setActive(true),
        onMouseLeave: () => setActive(false),
      })}
      {ReactDOM.createPortal(
        <span
          ref={tooltipRef}
          className={tooltipClass}
          style={{
            left: `${position.x}px`,
            top: `${position.y}px`,
            maxWidth: width,
            visibility: active ? 'visible' : 'hidden',
          }}
        >
          {title}
        </span>,
        tooltipRoot
      )}
    </>
  );
}
