import { useI18n } from '../../i18n';
import { useDialog } from '../../components/dialog';
import {
  useVerifiableValue,
  useExtraError,
  useEmailRules,
  usePasswordRules,
  useNicknameRules,
} from '../../hooks';
import { useState, useCallback } from 'react';
import { useNavigator } from '../../components/items-control';
import { buildProxySettingsDialog } from '../dialogs';
import { LaunchViewName, SWITCH_NAME_LAUNCH } from '../../views/launch-window/window';
import { useProtectUi } from '../common';
import { signUpAsync, openPredefineUrl } from '../../java-api/launch-window';

export default function useSignUpModel() {
  const { $t } = useI18n();
  const networkSettingsDialogToken = useDialog<boolean>(buildProxySettingsDialog);

  const emailField = useVerifiableValue<string>(useEmailRules($t), '');
  const passwordField = useVerifiableValue<string>(usePasswordRules($t), '');
  const passwordAgainField = useVerifiableValue<string>(
    useCallback(
      (config) => {
        config.addRule(
          (value) => value === passwordField.value,
          () => $t('error.invalidPasswordAgain')
        );
      },
      [$t, passwordField.value]
    ),
    ''
  );
  const nicknameField = useVerifiableValue<string>(useNicknameRules($t), '');

  // [WS-220729] We should not automatically check the checkbox of "I agree to the Terms of Service and Privacy Policy".
  const [agreeTerms, setAgreeTerms] = useState(false);
  const [globalErrorText, setGlobalErrorText] = useExtraError([
    emailField.value,
    passwordField.value,
    passwordAgainField.value,
    nicknameField.value,
  ]);
  const [extraEmailError, setExtraEmailError] = useExtraError([emailField.value]);
  const [disabledUserInput, setDisabledUserInput] = useState(false);
  const [showCheckServiceTermsWanring, setShowCheckServiceTermsWarning] = useState(false);
  const launchNavigator = useNavigator<LaunchViewName>(SWITCH_NAME_LAUNCH);

  const protectUi = useProtectUi(disabledUserInput, setDisabledUserInput);
  const confirmSignUp = protectUi(async () => {
    setGlobalErrorText('');
    setExtraEmailError('');

    if (!agreeTerms) {
      setShowCheckServiceTermsWarning(true);
      return;
    }

    if (
      !emailField.check() ||
      !passwordField.check() ||
      !passwordAgainField.check() ||
      !nicknameField.check()
    ) {
      return;
    }

    const response = await signUpAsync(emailField.value, passwordField.value, nicknameField.value);
    switch (response.state) {
      case 'success':
        launchNavigator('new-user-wizard');
        break;
      case 'need-phone':
        launchNavigator('phone-verification', {
          email: emailField.value,
          password: passwordField.value,
          nickname: nicknameField.value,
        });
        break;
      case 'failed':
        switch (response.errorPosition) {
          case 'global':
            setGlobalErrorText(response.message ? response.message : $t('error.unknown'));
            break;
          case 'email':
            setExtraEmailError(response.message);
            break;
        }
        break;
    }
  });
  const gotoLoginPage = () => launchNavigator('login');
  const openUserAgreementUrl = () => openPredefineUrl('user-agreement');
  const openPrivacyPolicy = () => openPredefineUrl('privacy-policy');
  const showNetworkSettingsDialog = networkSettingsDialogToken.show;

  return {
    disabledUserInput,
    showCheckServiceTermsWanring,
    emailField: {
      ...emailField,
      error: emailField.error ? emailField.error : extraEmailError,
    },
    passwordField,
    passwordAgainField,
    nicknameField,
    agreeTerms,
    globalErrorText,
    setShowCheckServiceTermsWarning,
    setAgreeTerms,
    confirmSignUp,
    gotoLoginPage,
    openUserAgreementUrl,
    openPrivacyPolicy,
    showNetworkSettingsDialog,
  };
}
