import { getRandomCode } from '../helpers';

const win = window as any;

export default class JavaTask<T extends (...args: any) => any> {
  private readonly funcName: string;
  private invokeToken: string = '';

  constructor(funcName: string) {
    this.funcName = funcName;
  }

  run(...args: Parameters<T>): Promise<ReturnType<T>> {
    const javaFunc = win[this.funcName] as Function;
    if (!javaFunc) {
      return Promise.reject(`Not found this function: ${this.funcName}`);
    }

    const id = getRandomCode(8);
    this.invokeToken = `JavaTask_${this.funcName}_${id}`;

    return new Promise((resolve, reject) => {
      // register func_resolve and func_reject
      win[this.invokeToken] = {
        resolve: (result: ReturnType<T>) => {
          this.dispose();
          resolve(result);
        },
        reject: (reason: any) => {
          this.dispose();
          reject(reason);
        },
      };

      // The id is always the last args.
      // Alse see: [project]/src/nutstore/client/webui/interop/WebFuncRegistry.java file.
      javaFunc.call(win, ...(args as any), id);
    });
  }

  dispose() {
    delete win[this.invokeToken];
  }
}
