import { useLazyRef } from '../hooks';
import { importJavaFunc } from '../java-interop';

type LogLevel = 'error' | 'warn' | 'info' | 'debug';

export const log = importJavaFunc<(level: LogLevel, message: string) => void>('log');
export const isDarkMode = importJavaFunc<() => boolean>('isDarkMode', false);
export const isChineseLocale = importJavaFunc<() => boolean>('isChineseLocale', false);
export const openUrl = importJavaFunc<(url: string) => void>('openUrl');
export const isEnterprise = importJavaFunc<() => boolean>('isEnterprise', false);
export const openPath = importJavaFunc<(path: string) => void>('openPath');
export const openFileWithApp = importJavaFunc<(path: string) => void>('openFileWithApp');
export const sendTeaEvent = importJavaFunc<(eventName: string, params?: string) => void>(
  'sendTeaEvent'
);

const isOSXPlatform = importJavaFunc<() => boolean>('isOSXPlatform');
export function useIsOSXPlatform() {
  return useLazyRef(isOSXPlatform);
}

const fileProviderEnabled = importJavaFunc<()=> boolean>('fileProviderEnabled');
export function useIsFileProviderEnabled() {
  return useLazyRef(fileProviderEnabled);
}

const badgeEnabled = importJavaFunc<() => boolean>('badgeEnabled');
export function useIsBadgeEnabled() {
  return useLazyRef(badgeEnabled);
}

const shouldShowUpgradeToFileProviderEntry = importJavaFunc<() => boolean>("shouldShowUpgradeToFileProviderEntry");
export function useShouldShowUpgradeToFileProviderEntry() {
  return useLazyRef(shouldShowUpgradeToFileProviderEntry);
}
