import React, { ReactNode, InputHTMLAttributes } from 'react';

interface CheckBoxProps extends InputHTMLAttributes<HTMLInputElement> {
  disabled?: boolean;
  children?: ReactNode;
  tooltip?: string;
}

export default function CheckBox({ disabled, children, tooltip, ...extraProps }: CheckBoxProps) {
  return (
    <div className="check-box">
      <input type="checkbox" id="input-tag" name="input-tag" disabled={disabled} {...extraProps} />
      <label htmlFor="input-tag">{children ?? ''}</label>
    </div>
  );
}
