import React, { InputHTMLAttributes, useState, ReactNode, Ref } from 'react';
import classNames from 'classnames';
import errorIcon from '../../assets/error_icon.svg';

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  disabled?: boolean;
  errorText?: string;
  addonBefore?: ReactNode;
  addonAfter?: ReactNode;
}

const Input = React.forwardRef(
  (
    { disabled, errorText, addonBefore, addonAfter, className, ...extraProps }: InputProps,
    ref: Ref<HTMLInputElement>
  ) => {
    const [focused, setFocused] = useState(false);
    const wrapperClass = classNames('input-wrapper', className, {
      focused: focused && !disabled,
      error: !!errorText,
      disabled,
    });

    return (
      <div className={wrapperClass} tabIndex={1}>
        {addonBefore}
        <input
          ref={ref}
          type="text"
          disabled={disabled}
          {...extraProps}
          onFocus={() => setFocused(true)}
          onBlur={() => setFocused(false)}
        />
        {errorText ? (
          <div className="error-text">
            <img src={errorIcon} alt="error-icon" width="12px" height="12px" />
            <span style={{ marginLeft: '6px' }}>{errorText}</span>
          </div>
        ) : null}
        {addonAfter}
      </div>
    );
  }
);

export default Input;
