import React from 'react';
import GeneralPreWindowView from './general-pre-window-view';
import { Button } from '../../components/button';
import { CheckBox, Input } from '../../components/input';
import { useI18n } from '../../i18n';
import useLoginEnterpriseModel from '../../models/launch-window/login-enterprise-model';

export default function EnterpriseServerView() {
    const { $t } = useI18n();
    const {
        disabledUserInput,
        serverAddressField,
        globalErrorText,
        useHttps,
        showNetworkSettingsDialog,
        openLearnMoreUrl,
        setUseHttps,
        confirmServerAddress,
    } = useLoginEnterpriseModel();

    return (
        <GeneralPreWindowView
            title={$t('loginEnterprise.title')}
            errorText={globalErrorText}
            footer={
                <Button
                    disabled={disabledUserInput}
                    buttonType="link"
                    onClick={async () => await showNetworkSettingsDialog()}
                >
                    {$t('login.networkSettings')}
                </Button>
            }
        >
            <Input
                placeholder={$t('loginEnterprise.serverAddress')}
                value={serverAddressField.value}
                onChange={(e) => serverAddressField.setOnly(e.target.value)}
                errorText={serverAddressField.error}
                disabled={disabledUserInput}
            />
            <span className="text tip">
                {$t('loginEnterprise.description')}
                <Button buttonType="link" onClick={() => openLearnMoreUrl()} style={{ fontSize: '12px' }}>
                    {$t('loginEnterprise.LearnMore')}
                </Button>
            </span>
            <CheckBox
                checked={useHttps}
                onChange={(e) => setUseHttps(e.target.checked)}
                disabled={disabledUserInput}
            >
                {$t('loginEnterprise.useSSL')}
            </CheckBox>
            <Button
                respondToEnter
                buttonType="primary"
                disabled={disabledUserInput}
                onClick={async () => await confirmServerAddress()}
            >
                {$t('button.next')}
            </Button>
        </GeneralPreWindowView>
    );
}