import { useClickCombo } from "./use-click-combo";

interface ContextmenuManger {
  enable(): void
  disable(): void
}

const preventFn = (event: Event) => event.preventDefault()

const contextmenuManager: ContextmenuManger = {
  enable() {
    window.removeEventListener('contextmenu', preventFn)
  },
  disable() {
    window.addEventListener('contextmenu', preventFn)
  },
}

export function getContextmenuManager(): ContextmenuManger {
  return contextmenuManager
}

export function useContextmenuClickCombo(): {
  click(): void
} {
  return useClickCombo({
    timeout: 1000 * 10,
    times: 10,
    onFire() {
      contextmenuManager.enable()
      alert('Contextmenu enabled')
    },
  }, []);
}
