import apkIcon from '../assets/file-icons/apk.svg';
import audioIcon from '../assets/file-icons/audio.svg';
import codeIcon from '../assets/file-icons/code.svg';
import dwgIcon from '../assets/file-icons/dwg.svg';
import epsIcon from '../assets/file-icons/eps.svg';
import excelIcon from '../assets/file-icons/excel.svg';
import folderIcon from '../assets/file-icons/folder.svg';
import gifIcon from '../assets/file-icons/gif.svg';
import imageIcon from '../assets/file-icons/image.svg';
import isoIcon from '../assets/file-icons/iso.svg';
import mdIcon from '../assets/file-icons/md.svg';
import nbmxIcon from '../assets/file-icons/nbmx.svg';
import ngmIcon from '../assets/file-icons/ngm.svg';
import nolIcon from '../assets/file-icons/nol.svg';
import pdfIcon from '../assets/file-icons/pdf.svg';
import pptIcon from '../assets/file-icons/ppt.svg';
import programIcon from '../assets/file-icons/program.svg';
import psIcon from '../assets/file-icons/ps.svg';
import tiffIcon from '../assets/file-icons/tiff.svg';
import txtIcon from '../assets/file-icons/txt.svg';
import unknownIcon from '../assets/file-icons/unknown.svg';
import videoIcon from '../assets/file-icons/video.svg';
import wordIcon from '../assets/file-icons/word.svg';
import xmindIcon from '../assets/file-icons/xmind.svg';
import zipIcon from '../assets/file-icons/zip.svg';

export type FileType =
  | 'ai'
  | 'apk'
  | 'audio'
  | 'code'
  | 'dwg'
  | 'eps'
  | 'excel'
  | 'folder'
  | 'gif'
  | 'image'
  | 'iso'
  | 'md'
  | 'nbmx'
  | 'ngm'
  | 'nol'
  | 'pdf'
  | 'ppt'
  | 'program'
  | 'ps'
  | 'tiff'
  | 'txt'
  | 'unknown'
  | 'video'
  | 'word'
  | 'xmind'
  | 'zip';

function initializeFileIconMap(): ReadonlyMap<FileType, string> {
  const map = new Map<FileType, string>();
  map.set('ai', aiIcon);
  map.set('apk', apkIcon);
  map.set('audio', audioIcon);
  map.set('code', codeIcon);
  map.set('dwg', dwgIcon);
  map.set('eps', epsIcon);
  map.set('excel', excelIcon);
  map.set('folder', folderIcon);
  map.set('gif', gifIcon);
  map.set('image', imageIcon);
  map.set('iso', isoIcon);
  map.set('md', mdIcon);
  map.set('nbmx', nbmxIcon);
  map.set('ngm', ngmIcon);
  map.set('nol', nolIcon);
  map.set('pdf', pdfIcon);
  map.set('ppt', pptIcon);
  map.set('program', programIcon);
  map.set('ps', psIcon);
  map.set('tiff', tiffIcon);
  map.set('txt', txtIcon);
  map.set('unknown', unknownIcon);
  map.set('video', videoIcon);
  map.set('word', wordIcon);
  map.set('xmind', xmindIcon);
  map.set('zip', zipIcon);
  return map;
}

const FILE_TYPE_MAP: ReadonlyArray<{ type: FileType; exts: ReadonlyArray<string> }> = [
  { type: 'ai', exts: ['ai'] },
  { type: 'apk', exts: ['apk'] },
  { type: 'audio', exts: ['mp3', 'wav', 'aac', 'wma', 'flac', 'ape', 'ogg'] },
  {
    type: 'code',
    exts: [
      'cs',
      'c',
      'cpp',
      'py',
      'java',
      'js',
      'ts',
      'xaml',
      'xml',
      'json',
      'yml',
      'yaml',
      'vb',
      'html',
      'htm',
      'css',
      'm',
      'go',
      'kt',
      'kts',
      'swift',
      'rs',
    ],
  },
  { type: 'dwg', exts: ['dwg'] },
  { type: 'eps', exts: ['eps'] },
  { type: 'excel', exts: ['xls', 'xlsx'] },
  { type: 'gif', exts: ['gif'] },
  { type: 'image', exts: ['png', 'jpg', 'jpeg', 'bmp', 'git'] },
  { type: 'iso', exts: ['iso'] },
  { type: 'md', exts: ['md'] },
  { type: 'nbmx', exts: ['nbmx'] },
  { type: 'ngm', exts: ['ngm'] },
  { type: 'nol', exts: ['nol'] },
  { type: 'pdf', exts: ['pdf'] },
  { type: 'ppt', exts: ['ppt', 'pptx'] },
  { type: 'program', exts: ['exe', 'bat'] },
  { type: 'ps', exts: ['psd'] },
  { type: 'tiff', exts: ['tiff'] },
  { type: 'txt', exts: ['txt', 'lrc'] },
  { type: 'video', exts: ['rmvb', 'mp4', 'avi', 'rm', 'wmv', 'flv', 'f4v', 'mkv', '3gp'] },
  { type: 'word', exts: ['doc', 'docx'] },
  { type: 'xmind', exts: ['xmind'] },
  { type: 'zip', exts: ['rar', 'zip', '7z'] },
];

const FILE_ICON_MAP = initializeFileIconMap();

export function getFileIcon(type: FileType): string {
  return FILE_ICON_MAP.get(type) ?? unknownIcon;
}

export function getFileType(path: string, asFolderIfEmptyExt = true): FileType {
  const ext = getFileExtension(path);
  if (!ext && asFolderIfEmptyExt) {
    return 'folder';
  }

  const matched = FILE_TYPE_MAP.find(({ exts }) => exts.some((item) => item === ext));
  return matched ? matched.type : 'unknown';
}

function getFileExtension(path: string): string {
  const lastDotIndex = path.lastIndexOf('.');
  const lastSeparatorIndex = Math.max(path.lastIndexOf('/'), path.lastIndexOf('\\'));
  return lastDotIndex < path.length && lastDotIndex > lastSeparatorIndex
    ? path.slice(lastDotIndex + 1)
    : '';
}
