import React, { InputHTMLAttributes, ReactNode, Ref } from 'react';
import { CSSTransition } from 'react-transition-group';
import classNames from 'classnames';

import searchIcon from '../../assets/main-window/search.svg';
import closeIcon from '../../assets/main-window/close.svg';
import { Tooltip } from '../tooltip';
import { useI18n } from '../../i18n';

interface SearchBoxProps extends InputHTMLAttributes<HTMLInputElement> {
  addon?: ReactNode;
  isEditing: boolean;
  setIsEditing: (value: boolean) => void;
}

const SearchBox = React.forwardRef(
  (
    { addon, isEditing, setIsEditing, className, ...extraProps }: SearchBoxProps,
    ref: Ref<HTMLInputElement>
  ) => {
    const { $t } = useI18n();
    const wrapperClass = classNames('search-box-wrapper', className);

    return (
      <div className={wrapperClass}>
        <img className="search-icon" src={searchIcon} alt="search-icon" />
        <input
          ref={ref}
          tabIndex={-1}
          className="search-input"
          type="text"
          onFocus={() => setIsEditing(true)}
          {...extraProps}
        />
        <div className="search-addon-wrapper">
          <CSSTransition in={isEditing} timeout={400} classNames="float-in" unmountOnExit>
            <Tooltip title={$t('button.cancel')}>
              <button className="search-close" onClick={() => setIsEditing(false)}>
                <img src={closeIcon} alt="close-icon" style={{ width: '16px', height: '16px' }} />
              </button>
            </Tooltip>
          </CSSTransition>
          <CSSTransition in={!isEditing} timeout={400} classNames="float-in" unmountOnExit>
            <div className="search-addon">{addon}</div>
          </CSSTransition>
        </div>
      </div>
    );
  }
);

export default SearchBox;
